import { useState, useCallback } from 'react';

export const useFullScreenLoader = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [message, setMessage] = useState('');
  const [type, setType] = useState('login');

  const showLoader = useCallback((loaderMessage = '', duration = 3000, loaderType = 'login') => {
    setMessage(loaderMessage);
    setType(loaderType);
    setIsVisible(true);
    
    if (duration > 0) {
      setTimeout(() => {
        setIsVisible(false);
      }, duration);
    }
  }, []);

  const hideLoader = useCallback(() => {
    setIsVisible(false);
  }, []);

  return {
    isVisible,
    message,
    type,
    showLoader,
    hideLoader
  };
};
