import React, { useEffect, useMemo } from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { store } from './store';
import { AppProvider } from './contexts/AppContext';
import { AuthProvider } from './contexts/AuthContext';
import { ConnectionStatusProvider, useConnectionStatus } from './contexts/ConnectionStatusContext';
import { ControlProvider } from './contexts/ControlContext';
import MainLayout from './components/Layout/MainLayout';
import ProtectedRoute from './components/ProtectedRoute';
import SiteUnreachableOverlay from './components/SiteUnreachableOverlay';
import { getBasePath } from './utils/helpers';
import Overview from './pages/Overview';
import Network from './pages/Network';
import Devices from './pages/Devices';
import Logs from './pages/Logs';
import Alerts from './pages/Alerts';
import Settings from './pages/Settings';
import Control from './pages/Control';
import socketService from './services/socketService';
import healthCheckService from './services/healthCheckService';
import './App.css';

function AppContent() {
  const { setReachable } = useConnectionStatus();
  const basename = useMemo(() => getBasePath(), []);

  useEffect(() => {
    socketService.connect();
    healthCheckService.start(setReachable);
    
    return () => {
      socketService.disconnect();
      healthCheckService.stop();
    };
  }, [setReachable]);

  return (
    <>
      <SiteUnreachableOverlay />
      <Router basename={basename}>
        <div className="App">
          <MainLayout>
            <Routes>
              <Route path="/" element={<Overview />} />
              <Route path="/overview" element={<Overview />} />
              <Route path="/logs" element={<Logs />} />
              <Route path="/alerts" element={<Alerts />} />
              
              {/* Protected Routes */}
              <Route path="/network" element={
                <ProtectedRoute>
                  <Network />
                </ProtectedRoute>
              } />
              <Route path="/devices" element={<Devices />} />
              <Route path="/settings" element={
                <ProtectedRoute>
                  <Settings />
                </ProtectedRoute>
              } />
              <Route path="/control" element={
                <ProtectedRoute>
                  <Control />
                </ProtectedRoute>
              } />
              
              <Route path="*" element={<Navigate to="/" replace />} />
            </Routes>
          </MainLayout>
          
          {/* Global Toast Container */}
          <ToastContainer
            position="top-right"
            autoClose={5000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            theme="light"
            toastClassName="!bg-white/95 !backdrop-blur-sm !border !border-[#0097b2]/20 !shadow-xl"
            progressClassName="!bg-gradient-to-r !from-[#0097b2] !to-[#7ed957]"
            style={{ zIndex: 1000001 }}
          />
        </div>
      </Router>
    </>
  );
}

function App() {
  return (
    <Provider store={store}>
      <AuthProvider>
        <AppProvider>
          <ConnectionStatusProvider>
            <ControlProvider>
            <AppContent />
            </ControlProvider>
          </ConnectionStatusProvider>
        </AppProvider>
      </AuthProvider>
    </Provider>
  );
}

export default App; 