const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5001';

class HealthCheckService {
  constructor() {
    this.intervalId = null;
    this.isRunning = false;
    this.checkInterval = 3000; // 3 seconds
    this.consecutiveFailures = 0;
    this.failureThreshold = 2; // Refresh after 2+ failures
  }

  start(setReachable) {
    if (this.isRunning) {
      return;
    }

    this.isRunning = true;
    this.setReachable = setReachable;

    // Perform immediate check
    this.checkHealth();

    // Set up interval
    this.intervalId = setInterval(() => {
      this.checkHealth();
    }, this.checkInterval);
  }

  async checkHealth() {
    try {
      const response = await fetch(`${API_BASE_URL}/health`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (response.ok && response.status === 200) {
        const data = await response.json();
        if (data && data.status === 'ok') {
          // If we had more than 2 failures and now we're back, refresh the page
          if (this.consecutiveFailures > this.failureThreshold) {
            window.location.reload();
            return;
          }
          
          // Reset failure counter on success
          this.consecutiveFailures = 0;
          
          if (this.setReachable) {
            this.setReachable(true);
          }
        } else {
          // Response OK but status not 'ok'
          this.consecutiveFailures++;
          if (this.setReachable) {
            this.setReachable(false);
          }
        }
      } else {
        // Not 200 OK
        this.consecutiveFailures++;
        if (this.setReachable) {
          this.setReachable(false);
        }
      }
    } catch (error) {
      // Network error or any other error means backend is unreachable
      this.consecutiveFailures++;
      if (this.setReachable) {
        this.setReachable(false);
      }
    }
  }

  stop() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
      this.intervalId = null;
    }
    this.isRunning = false;
    this.setReachable = null;
  }
}

const healthCheckService = new HealthCheckService();
export default healthCheckService;

