import React from 'react';
import { createPortal } from 'react-dom';
import { X, Settings } from 'lucide-react';

const EditPortModal = ({ isOpen, portName, values, options, onClose, onSave, onChange }) => {
  if (!isOpen) return null;
  
  const { baud = '', dataBits = '', stopBits = '', parity = '', mode = '' } = values || {};
  const dd = options || { baud: [], dataBits: [], stopBits: [], parity: [], mode: [] };

  return createPortal(
    <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50 p-4">
      <div className="bg-white dark:bg-gray-900/70 backdrop-blur-xl rounded-3xl shadow-2xl w-full max-w-lg max-h-[85vh] overflow-hidden border border-green-200/40 relative">
        {/* Gradient background */}
        <div className="absolute inset-0 bg-gradient-to-r from-[#8c7ae6]/5 via-[#a78bfa]/8 to-[#8c7ae6]/5"></div>
        
        {/* Header */}
        <div className="bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] p-6 text-white relative">
          <div className="absolute inset-0 bg-gradient-to-r from-white/10 to-transparent"></div>
          <div className="relative flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-white dark:bg-gray-900/70 rounded-lg flex items-center justify-center backdrop-blur-sm">
                <Settings className="w-5 h-5" />
              </div>
              <div>
                <h2 className="text-xl font-bold">Edit {portName} Settings</h2>
                <p className="text-white/90 text-sm">Configure serial port parameters</p>
              </div>
            </div>
            <button
              onClick={onClose}
              className="text-white/80 hover:text-white transition-all duration-200 p-2 rounded-lg hover:bg-white dark:bg-gray-900/70 backdrop-blur-sm"
            >
              <X className="w-5 h-5" />
            </button>
          </div>
        </div>

        <div className="relative z-10 p-6 overflow-y-auto max-h-[calc(85vh-120px)]">

          {/* Form */}
          <form onSubmit={(e) => { e.preventDefault(); onSave(); }} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">Baud Rate</label>
              <select
                value={baud}
                onChange={(e) => onChange('baud', e.target.value)}
                className="w-full border border-gray-300 rounded-lg px-4 py-3 bg-white dark:bg-gray-900/70 backdrop-blur-sm focus:ring-2 focus:ring-[#8c7ae6]/50 focus:border-[#8c7ae6] transition-all duration-200"
              >
                <option value="">Select baud rate</option>
                {dd.baud.map(rate => (
                  <option key={rate} value={rate}>{rate}</option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Data Bits</label>
              <select
                value={dataBits}
                onChange={(e) => onChange('dataBits', e.target.value)}
                className="w-full border border-gray-300 rounded-lg px-4 py-3 bg-white dark:bg-gray-900/70 backdrop-blur-sm focus:ring-2 focus:ring-[#8c7ae6]/50 focus:border-[#8c7ae6] transition-all duration-200"
              >
                <option value="">Select data bits</option>
                {dd.dataBits.map(bits => (
                  <option key={bits} value={bits}>{bits}</option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Stop Bits</label>
              <select
                value={stopBits}
                onChange={(e) => onChange('stopBits', e.target.value)}
                className="w-full border border-gray-300 rounded-lg px-4 py-3 bg-white dark:bg-gray-900/70 backdrop-blur-sm focus:ring-2 focus:ring-[#8c7ae6]/50 focus:border-[#8c7ae6] transition-all duration-200"
              >
                <option value="">Select stop bits</option>
                {dd.stopBits.map(bits => (
                  <option key={bits} value={bits}>{bits}</option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Parity</label>
              <select
                value={parity}
                onChange={(e) => onChange('parity', e.target.value)}
                className="w-full border border-gray-300 rounded-lg px-4 py-3 bg-white dark:bg-gray-900/70 backdrop-blur-sm focus:ring-2 focus:ring-[#8c7ae6]/50 focus:border-[#8c7ae6] transition-all duration-200"
              >
                <option value="">Select parity</option>
                {dd.parity.map(p => (
                  <option key={p} value={p}>{p.charAt(0).toUpperCase() + p.slice(1)}</option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Mode</label>
              <select
                value={mode}
                onChange={(e) => onChange('mode', e.target.value)}
                className="w-full border border-gray-300 rounded-lg px-4 py-3 bg-white dark:bg-gray-900/70 backdrop-blur-sm focus:ring-2 focus:ring-[#8c7ae6]/50 focus:border-[#8c7ae6] transition-all duration-200"
              >
                <option value="">Select mode</option>
                {dd.mode.map(m => (
                  <option key={m} value={m}>{m.charAt(0).toUpperCase() + m.slice(1)}</option>
                ))}
              </select>
            </div>

            {/* Action Buttons */}
            <div className="flex gap-4 pt-2">
              <button
                type="button"
                onClick={onClose}
                className="flex-1 px-6 py-3 border border-gray-300 dark:border-gray-700 text-gray-700 dark:text-gray-300 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-800 transition-all duration-200 font-semibold text-sm shadow-sm hover:shadow-md"
              >
                Cancel
              </button>
              <button
                type="submit"
                className="flex-1 px-6 py-3 bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] text-white rounded-lg hover:from-[#007a93] hover:to-[#147015] transition-all duration-200 font-semibold text-sm shadow-md hover:shadow-lg"
              >
                Save Changes
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>,
    document.body
  );
};

export default EditPortModal;
