import React, { useState, useRef, useEffect } from 'react';
import { createPortal } from 'react-dom';
import { ChevronDown, X, Check, Search } from 'lucide-react';

const MultiSelect = ({
  options = [],
  selected = [],
  onChange,
  placeholder = 'Select options...',
  label,
  disabled = false,
  className = '',
  searchable = true,
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [dropdownPosition, setDropdownPosition] = useState({ top: 0, left: 0, width: 0 });
  const dropdownRef = useRef(null);
  const containerRef = useRef(null);
  const searchInputRef = useRef(null);

  // Close dropdown when clicking outside
  useEffect(() => {
    if (!isOpen) return;

    const handleClickOutside = (event) => {
      const target = event.target;
      // Check if click is outside both the container and the dropdown portal
      if (
        dropdownRef.current && 
        !dropdownRef.current.contains(target) &&
        !target.closest('[data-multiselect-dropdown]')
      ) {
        setIsOpen(false);
        setSearchQuery(''); // Clear search when closing
      }
    };

    // Use a small delay to avoid immediate closing when opening
    const timeoutId = setTimeout(() => {
      document.addEventListener('mousedown', handleClickOutside);
    }, 100);

    return () => {
      clearTimeout(timeoutId);
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen]);

  // Calculate dropdown position when opening
  useEffect(() => {
    if (isOpen && containerRef.current) {
      const rect = containerRef.current.getBoundingClientRect();
      const viewportHeight = window.innerHeight;
      const viewportWidth = window.innerWidth;
      const dropdownMaxHeight = 240; // max-h-60 = 240px
      
      // Calculate if dropdown should open above or below
      const spaceBelow = viewportHeight - rect.bottom;
      const spaceAbove = rect.top;
      const openAbove = spaceBelow < dropdownMaxHeight && spaceAbove > spaceBelow;
      
      // Calculate left position (ensure it doesn't go off-screen)
      let left = rect.left + window.scrollX;
      const dropdownWidth = rect.width;
      if (left + dropdownWidth > viewportWidth + window.scrollX) {
        left = viewportWidth + window.scrollX - dropdownWidth - 10;
      }
      if (left < window.scrollX) {
        left = window.scrollX + 10;
      }
      
      setDropdownPosition({
        top: openAbove 
          ? rect.top + window.scrollY - dropdownMaxHeight - 4
          : rect.bottom + window.scrollY + 4,
        left: left,
        width: dropdownWidth
      });
    }
  }, [isOpen]);

  // Update position on scroll/resize
  useEffect(() => {
    if (!isOpen) return;

    const updatePosition = () => {
      if (containerRef.current) {
        const rect = containerRef.current.getBoundingClientRect();
        const viewportHeight = window.innerHeight;
        const viewportWidth = window.innerWidth;
        const dropdownMaxHeight = 240;
        
        const spaceBelow = viewportHeight - rect.bottom;
        const spaceAbove = rect.top;
        const openAbove = spaceBelow < dropdownMaxHeight && spaceAbove > spaceBelow;
        
        let left = rect.left + window.scrollX;
        const dropdownWidth = rect.width;
        if (left + dropdownWidth > viewportWidth + window.scrollX) {
          left = viewportWidth + window.scrollX - dropdownWidth - 10;
        }
        if (left < window.scrollX) {
          left = window.scrollX + 10;
        }
        
        setDropdownPosition({
          top: openAbove 
            ? rect.top + window.scrollY - dropdownMaxHeight - 4
            : rect.bottom + window.scrollY + 4,
          left: left,
          width: dropdownWidth
        });
      }
    };

    window.addEventListener('scroll', updatePosition, true);
    window.addEventListener('resize', updatePosition);

    return () => {
      window.removeEventListener('scroll', updatePosition, true);
      window.removeEventListener('resize', updatePosition);
    };
  }, [isOpen]);

  // Focus search input when dropdown opens
  useEffect(() => {
    if (isOpen && searchable && searchInputRef.current) {
      setTimeout(() => {
        searchInputRef.current?.focus();
      }, 100);
    }
  }, [isOpen, searchable]);

  // Filter options based on search query
  const filteredOptions = searchable && searchQuery.trim()
    ? options.filter(option =>
        option.label.toLowerCase().includes(searchQuery.toLowerCase()) ||
        option.value.toLowerCase().includes(searchQuery.toLowerCase())
      )
    : options;

  const toggleOption = (optionValue) => {
    if (disabled) return;
    
    const newSelected = selected.includes(optionValue)
      ? selected.filter((item) => item !== optionValue)
      : [...selected, optionValue];
    
    onChange(newSelected);
  };

  const removeOption = (optionValue, e) => {
    e.stopPropagation();
    if (disabled) return;
    
    const newSelected = selected.filter((item) => item !== optionValue);
    onChange(newSelected);
  };

  const clearAll = (e) => {
    e.stopPropagation();
    if (disabled) return;
    onChange([]);
  };

  const getDisplayText = () => {
    if (selected.length === 0) {
      return placeholder;
    }
    if (selected.length === 1) {
      const option = options.find((opt) => opt.value === selected[0]);
      return option ? option.label : selected[0];
    }
    return `${selected.length} selected`;
  };

  return (
    <>
      <style>{`
        .multi-select-scrollbar::-webkit-scrollbar {
          width: 6px;
        }
        .multi-select-scrollbar::-webkit-scrollbar-track {
          background: transparent;
        }
        .multi-select-scrollbar::-webkit-scrollbar-thumb {
          background: #8c7ae6;
          border-radius: 3px;
        }
        .multi-select-scrollbar::-webkit-scrollbar-thumb:hover {
          background: #a78bfa;
        }
      `}</style>
      <div className={`relative ${className}`} ref={dropdownRef}>
        {label && (
          <label className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-2">
            {label}
          </label>
        )}
      
      <div
        ref={containerRef}
        onClick={() => !disabled && setIsOpen(!isOpen)}
        className={`
          w-full border rounded-lg px-4 py-2.5 text-sm 
          focus:ring-2 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] 
          bg-white dark:bg-gray-900/70
          cursor-pointer transition-all duration-200
          ${disabled ? 'opacity-50 cursor-not-allowed' : 'hover:border-[#8c7ae6]/50 hover:shadow-sm'}
          ${isOpen ? 'border-[#8c7ae6] ring-2 ring-[#8c7ae6]/30 shadow-md' : 'border-gray-300 dark:border-gray-600'}
        `}
      >
        <div className="flex items-center justify-between gap-2">
          <div className="flex-1 flex items-center gap-2 flex-wrap min-h-[24px]">
            {selected.length === 0 ? (
              <span className="text-gray-500 dark:text-gray-400">{placeholder}</span>
            ) : (
              <>
                {selected.slice(0, 2).map((value) => {
                  const option = options.find((opt) => opt.value === value);
                  return (
                    <span
                      key={value}
                      className="inline-flex items-center gap-1.5 px-2.5 py-1 bg-gradient-to-r from-[#8c7ae6]/15 to-[#a78bfa]/15 text-[#8c7ae6] dark:text-[#a78bfa] rounded-md text-xs font-semibold border border-[#8c7ae6]/20 shadow-sm"
                    >
                      {option ? option.label : value}
                      {!disabled && (
                        <button
                          type="button"
                          onClick={(e) => removeOption(value, e)}
                          className="hover:bg-[#8c7ae6]/30 rounded-full p-0.5 transition-all duration-150 hover:scale-110"
                        >
                          <X className="w-3 h-3" />
                        </button>
                      )}
                    </span>
                  );
                })}
                {selected.length > 2 && (
                  <span className="text-sm text-[#8c7ae6] font-medium">
                    +{selected.length - 2} more
                  </span>
                )}
              </>
            )}
          </div>
          <div className="flex items-center gap-1">
            {selected.length > 0 && !disabled && (
              <button
                type="button"
                onClick={clearAll}
                className="p-1 hover:bg-gray-100 dark:hover:bg-gray-700 rounded transition-colors"
              >
                <X className="w-4 h-4 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300" />
              </button>
            )}
            <ChevronDown
              className={`w-4 h-4 text-gray-400 dark:text-gray-500 transition-all duration-200 ${
                isOpen ? 'transform rotate-180 text-[#8c7ae6] dark:text-[#a78bfa]' : ''
              }`}
            />
          </div>
        </div>
      </div>

      {isOpen && !disabled && createPortal(
        <div 
          data-multiselect-dropdown
          className="fixed z-[99999] bg-white dark:bg-gray-900 border border-[#8c7ae6]/20 dark:border-[#8c7ae6]/30 rounded-xl shadow-2xl overflow-hidden backdrop-blur-sm"
          style={{
            top: `${dropdownPosition.top}px`,
            left: `${dropdownPosition.left}px`,
            width: `${dropdownPosition.width}px`,
          }}
        >
          {/* Search Input */}
          {searchable && (
            <div className="p-3 border-b border-gray-200 dark:border-gray-700 bg-gradient-to-r from-[#8c7ae6]/5 to-[#a78bfa]/5">
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Search className="h-4 w-4 text-[#8c7ae6] dark:text-[#a78bfa]" />
                </div>
                <input
                  ref={searchInputRef}
                  type="text"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  onClick={(e) => e.stopPropagation()}
                  className="block w-full pl-10 pr-3 py-2.5 text-sm border border-[#8c7ae6]/20 dark:border-[#8c7ae6]/30 rounded-lg 
                    bg-white dark:bg-gray-800/50
                    text-gray-900 dark:text-gray-100
                    placeholder-gray-400 dark:placeholder-gray-500
                    focus:ring-2 focus:ring-[#8c7ae6]/50 focus:border-[#8c7ae6] 
                    transition-all duration-200"
                  placeholder="Search options..."
                />
                {searchQuery && (
                  <button
                    type="button"
                    onClick={(e) => {
                      e.stopPropagation();
                      setSearchQuery('');
                      searchInputRef.current?.focus();
                    }}
                    className="absolute inset-y-0 right-0 pr-3 flex items-center"
                  >
                    <X className="h-4 w-4 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300 transition-colors" />
                  </button>
                )}
              </div>
            </div>
          )}

          {/* Options List */}
          <div className="max-h-60 overflow-auto multi-select-scrollbar">
            {filteredOptions.length === 0 ? (
              <div className="px-4 py-8 text-center">
                <div className="flex flex-col items-center gap-2">
                  <Search className="w-8 h-8 text-gray-300 dark:text-gray-600" />
                  <p className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    {searchQuery ? 'No options found' : 'No options available'}
                  </p>
                  {searchQuery && (
                    <p className="text-xs text-gray-400 dark:text-gray-500">
                      Try a different search term
                    </p>
                  )}
                </div>
              </div>
            ) : (
              <div className="p-2">
                {filteredOptions.map((option) => {
                  const isSelected = selected.includes(option.value);
                  return (
                    <div
                      key={option.value}
                      onClick={() => toggleOption(option.value)}
                      className={`
                        px-3 py-2.5 rounded-lg cursor-pointer transition-all duration-200
                        flex items-center gap-3 mb-1
                        ${isSelected 
                          ? 'bg-gradient-to-r from-[#8c7ae6]/15 to-[#a78bfa]/15 text-[#8c7ae6] dark:text-[#a78bfa] shadow-sm border border-[#8c7ae6]/20' 
                          : 'hover:bg-gradient-to-r hover:from-[#8c7ae6]/5 hover:to-[#a78bfa]/5 text-gray-700 dark:text-gray-300 border border-transparent'
                        }
                      `}
                    >
                      <div
                        className={`
                          w-5 h-5 rounded-md border-2 flex items-center justify-center transition-all duration-200 flex-shrink-0
                          ${isSelected 
                            ? 'bg-gradient-to-br from-[#8c7ae6] to-[#a78bfa] border-[#8c7ae6] shadow-md shadow-[#8c7ae6]/30' 
                            : 'border-gray-300 dark:border-gray-600 hover:border-[#8c7ae6]/50'
                          }
                        `}
                      >
                        {isSelected && <Check className="w-3.5 h-3.5 text-white" strokeWidth={3} />}
                      </div>
                      <span className="text-sm font-medium flex-1">{option.label}</span>
                      {option.icon && (
                        <div className={`flex-shrink-0 ${isSelected ? 'text-[#8c7ae6] dark:text-[#a78bfa]' : (option.color || 'text-gray-400')}`}>
                          <option.icon className="w-4 h-4" />
                        </div>
                      )}
                    </div>
                  );
                })}
              </div>
            )}
          </div>

          {/* Selected Count Footer */}
          {selected.length > 0 && (
            <div className="px-4 py-2.5 border-t border-gray-200 dark:border-gray-700 bg-gradient-to-r from-[#8c7ae6]/5 to-[#a78bfa]/5">
              <div className="flex items-center justify-between">
                <span className="text-xs font-medium text-[#8c7ae6] dark:text-[#a78bfa]">
                  {selected.length} {selected.length === 1 ? 'item' : 'items'} selected
                </span>
                <button
                  type="button"
                  onClick={(e) => {
                    e.stopPropagation();
                    clearAll(e);
                  }}
                  className="text-xs text-gray-500 hover:text-[#8c7ae6] dark:hover:text-[#a78bfa] transition-colors font-medium"
                >
                  Clear all
                </button>
              </div>
            </div>
          )}
        </div>
      , document.body)}
      </div>
    </>
  );
};

export default MultiSelect;

