import React from 'react';

/**
 * Card Component - Matches gfe-sphere styling exactly
 * 
 * Standard container with consistent styling across the app
 */

export const Card = ({ children, className = '', noPadding = false }) => {
  return (
    <div className={`bg-white dark:bg-gray-900/70 rounded-lg border border-[#24292e1f] dark:border-gray-800/50 ${!noPadding ? 'px-4 py-4' : ''} ${className}`}>
      {children}
    </div>
  );
};

/**
 * CardHeader Component - For card titles and descriptions
 */
export const CardHeader = ({ title, description, children, className = '' }) => {
  return (
    <div className={`px-4 py-3 border-b border-[#24292e1f] dark:border-gray-700 ${className}`}>
      {title && (
        <h2 className="text-xl font-bold text-gray-900 dark:text-white">
          {title}
        </h2>
      )}
      {description && (
        <p className="text-sm text-gray-600 dark:text-gray-300 mt-1">
          {description}
        </p>
      )}
      {children}
    </div>
  );
};

/**
 * CardContent Component - For card body content
 */
export const CardContent = ({ children, className = '' }) => {
  return (
    <div className={`px-4 py-4 ${className}`}>
      {children}
    </div>
  );
};

/**
 * StatCard Component - For dashboard statistics
 */
export const StatCard = ({ label, value, icon, trend, className = '' }) => {
  return (
    <div className={`bg-white dark:bg-gray-900/70 rounded-lg border border-[#24292e1f] dark:border-gray-800/50 overflow-hidden shadow-sm hover:shadow-md transition-shadow duration-200 ${className}`}>
      <div className="px-4 py-4">
        <div className="flex items-center justify-between">
          <div className="flex-1">
            <p className="text-sm font-medium text-gray-600 dark:text-gray-300 mb-1">
              {label}
            </p>
            <p className="text-2xl font-bold text-gray-900 dark:text-white">
              {value}
            </p>
            {trend && (
              <p className={`text-xs mt-1 ${trend.isPositive ? 'text-green-600 dark:text-green-400' : 'text-red-600 dark:text-red-400'}`}>
                {trend.text}
              </p>
            )}
          </div>
          {icon && (
            <div className="flex-shrink-0 text-primary-500 dark:text-primary-400">
              {icon}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

/**
 * FilterButton Component - For filter pills (like in Overview page)
 */
export const FilterButton = ({ 
  label, 
  count, 
  isActive = false, 
  onClick, 
  color = 'blue',
  className = '' 
}) => {
  const colorConfig = {
    blue: {
      active: 'bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-md shadow-blue-500/25 border-blue-400',
      inactive: 'bg-white dark:bg-gray-900/70 dark:bg-gray-900/70 text-gray-700 dark:text-gray-300 hover:bg-blue-50 dark:hover:bg-blue-900/20 border-gray-200 dark:border-gray-700 dark:border-gray-700',
    },
    green: {
      active: 'bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] text-white shadow-md shadow-[#8c7ae6]/25 border-[#8c7ae6]',
      inactive: 'bg-white dark:bg-gray-900/70 dark:bg-gray-900/70 text-gray-700 dark:text-gray-300 hover:bg-purple-50 dark:hover:bg-purple-900/20 border-gray-200 dark:border-gray-700 dark:border-gray-700',
    },
    red: {
      active: 'bg-gradient-to-r from-red-500 to-red-600 text-white shadow-md shadow-red-500/25 border-red-400',
      inactive: 'bg-white dark:bg-gray-900/70 dark:bg-gray-900/70 text-gray-700 dark:text-gray-300 hover:bg-red-50 dark:hover:bg-red-900/20 border-gray-200 dark:border-gray-700 dark:border-gray-700',
    },
    purple: {
      active: 'bg-gradient-to-r from-purple-500 to-purple-600 text-white shadow-md shadow-purple-500/25 border-purple-400',
      inactive: 'bg-white dark:bg-gray-900/70 dark:bg-gray-900/70 text-gray-700 dark:text-gray-300 hover:bg-purple-50 dark:hover:bg-purple-900/20 border-gray-200 dark:border-gray-700 dark:border-gray-700',
    },
    gray: {
      active: 'bg-gradient-to-r from-gray-500 to-gray-600 text-white shadow-md shadow-gray-500/25 border-gray-400',
      inactive: 'bg-white dark:bg-gray-900/70 dark:bg-gray-900/70 text-gray-700 dark:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-900/20 border-gray-200 dark:border-gray-700 dark:border-gray-700',
    },
  };

  const config = colorConfig[color] || colorConfig.blue;
  const classes = isActive ? config.active : config.inactive;

  return (
    <button
      type="button"
      onClick={onClick}
      className={`px-3 py-1.5 rounded-lg font-medium text-xs transition-all duration-300 transform hover:scale-[1.02] active:scale-[0.98] border-2 ${classes} ${className}`}
    >
      <span className="flex items-center gap-1.5">
        <span>{label}</span>
        {count !== undefined && (
          <span className={`px-1.5 py-0.5 rounded-full text-xs font-bold ${
            isActive 
              ? 'bg-white dark:bg-gray-900/70 text-white' 
              : 'bg-gray-100 dark:bg-gray-700 text-gray-600 dark:text-gray-300'
          }`}>
            {count}
          </span>
        )}
      </span>
    </button>
  );
};

/**
 * EmptyState Component - For empty data states
 */
export const EmptyState = ({ icon, title, description, action, className = '' }) => {
  return (
    <div className={`flex items-center justify-center p-20 ${className}`}>
      <div className="text-center max-w-md">
        {icon && (
          <div className="inline-flex items-center justify-center w-20 h-20 rounded-lg bg-gradient-to-br from-gray-100 to-gray-200 dark:from-gray-800 dark:to-gray-700 mb-6 shadow-inner">
            {icon}
          </div>
        )}
        <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-2">
          {title}
        </h3>
        <p className="text-sm text-gray-600 dark:text-gray-300 leading-relaxed mb-4">
          {description}
        </p>
        {action}
      </div>
    </div>
  );
};

export default Card;

