import React from 'react';
import { createPortal } from 'react-dom';
import { Power, X, AlertCircle } from 'lucide-react';

const ControlModuleConfirmModal = ({ isOpen, isEnabled, onConfirm, onCancel }) => {
  if (!isOpen) return null;

  const actionTitle = isEnabled ? 'Stop Control Service' : 'Start Control Service';
  const mainMessage = isEnabled 
    ? 'Your control service will stop if you switch off.'
    : 'Your control service will start if you switch on.';
  const additionalInfo = isEnabled
    ? 'All configuration fields will become editable once the control service is stopped.'
    : 'All configuration fields will be locked once the control service is started.';

  return createPortal(
    <div 
      className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50 p-4 animate-fadeIn"
      onClick={onCancel}
    >
      <div 
        className="bg-white dark:bg-gray-900/70 backdrop-blur-xl rounded-2xl shadow-2xl w-full max-w-md overflow-hidden border border-[#8c7ae6]/20 dark:border-[#a78bfa]/20 transform transition-all duration-300 scale-100"
        onClick={(e) => e.stopPropagation()}
      >
        {/* Header - App Branding Purple Gradient */}
        <div className="bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] p-6 text-white relative">
          <div className="absolute inset-0 bg-gradient-to-r from-white/10 to-transparent"></div>
          <div className="relative flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-white/20 dark:bg-gray-900/30 rounded-xl flex items-center justify-center backdrop-blur-sm">
                <Power className="w-6 h-6 text-white" />
              </div>
              <div>
                <h2 className="text-xl font-bold">{actionTitle}</h2>
                <p className="text-white/90 text-sm mt-0.5">Confirm your action</p>
              </div>
            </div>
            <button
              onClick={onCancel}
              className="text-white/80 hover:text-white transition-all duration-200 p-2 rounded-lg hover:bg-white/20 backdrop-blur-sm"
              aria-label="Close"
            >
              <X className="w-5 h-5" />
            </button>
          </div>
        </div>

        {/* Content */}
        <div className="p-6">
          <div className="mb-6">
            {/* Main Message */}
            <div className="flex items-start gap-3 mb-4">
              <div className={`flex-shrink-0 w-10 h-10 rounded-xl flex items-center justify-center ${
                isEnabled 
                  ? 'bg-amber-100 dark:bg-amber-900/30' 
                  : 'bg-green-100 dark:bg-green-900/30'
              }`}>
                <AlertCircle className={`w-5 h-5 ${
                  isEnabled 
                    ? 'text-amber-600 dark:text-amber-400' 
                    : 'text-green-600 dark:text-green-400'
                }`} />
              </div>
              <div className="flex-1">
                <p className="text-gray-800 dark:text-gray-200 font-medium leading-relaxed text-base">
                  {mainMessage}
                </p>
              </div>
            </div>

            {/* Additional Info */}
            <div className={`p-4 rounded-xl border ${
              isEnabled
                ? 'bg-amber-50 dark:bg-amber-900/20 border-amber-200 dark:border-amber-800/50'
                : 'bg-green-50 dark:bg-green-900/20 border-green-200 dark:border-green-800/50'
            }`}>
              <p className={`text-sm leading-relaxed ${
                isEnabled
                  ? 'text-amber-800 dark:text-amber-200'
                  : 'text-green-800 dark:text-green-200'
              }`}>
                {additionalInfo}
              </p>
            </div>
          </div>

          {/* Actions */}
          <div className="flex gap-3">
            <button
              onClick={onCancel}
              className="flex-1 px-4 py-3 bg-gray-100 dark:bg-gray-800 text-gray-700 dark:text-gray-300 rounded-xl hover:bg-gray-200 dark:hover:bg-gray-700 transition-all duration-200 font-medium shadow-sm hover:shadow-md"
            >
              Cancel
            </button>
            <button
              onClick={onConfirm}
              className="flex-1 px-4 py-3 bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] hover:from-[#7c6ad6] hover:to-[#9778ea] text-white rounded-xl font-medium transition-all duration-200 shadow-lg hover:shadow-xl shadow-[#8c7ae6]/30"
            >
              {isEnabled ? 'Switch Off' : 'Switch On'}
            </button>
          </div>
        </div>
      </div>
    </div>,
    document.body
  );
};

export default ControlModuleConfirmModal;
