import React from 'react';

/**
 * PageContainer Component - Matches gfe-sphere's page layout exactly
 * 
 * Standard page wrapper with consistent spacing and background
 */

export const PageContainer = ({ children, className = '' }) => {
  return (
    <div className={`min-h-screen pb-1 ${className}`}>
      <div className="w-full pt-2 space-y-5">
        {children}
      </div>
    </div>
  );
};

/**
 * PageHeader Component - For page titles and actions
 */
export const PageHeader = ({ title, description, actions, className = '' }) => {
  return (
    <div className={`bg-white dark:bg-gray-900/70 rounded-lg border border-[#24292e1f] dark:border-gray-800/50 px-4 py-4 ${className}`}>
      <div className="flex items-center justify-between">
        <div className="flex-1">
          {title && (
            <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
              {title}
            </h1>
          )}
          {description && (
            <p className="text-sm text-gray-600 dark:text-gray-400 mt-1">
              {description}
            </p>
          )}
        </div>
        {actions && (
          <div className="flex items-center gap-2 ml-4">
            {actions}
          </div>
        )}
      </div>
    </div>
  );
};

/**
 * PageSection Component - For logical page sections
 */
export const PageSection = ({ children, className = '' }) => {
  return (
    <div className={`space-y-4 ${className}`}>
      {children}
    </div>
  );
};

export default PageContainer;

