import React, { useState, useEffect } from 'react';
import { useAppSelector } from '../hooks/redux';
import SortableTableHeader from '../components/SortableTableHeader';
import Pagination from '../components/Pagination';
import Spinner from '../components/Spinner';
import MultiSelect from '../components/MultiSelect';
import DateTimePicker from '../components/DateTimePicker';
import {
  FileText,
  Info,
  Filter,
  Search,
  RefreshCw,
  AlertTriangle,
  XCircle,
  Settings,
  Activity,
  ChevronLeft,
  ChevronRight,
  ArrowUpDown,
  ArrowUp,
  ArrowDown,
} from 'lucide-react';
import { toast } from 'react-toastify';
import ApiService from '../services/apiService';

const Logs = () => {
  const { isConnected } = useAppSelector((state) => state.sensor);

  // Filter states
  const [startDateTime, setStartDateTime] = useState('');
  const [endDateTime, setEndDateTime] = useState('');
  const [selectedTypes, setSelectedTypes] = useState([]);
  const [selectedDevices, setSelectedDevices] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [dateError, setDateError] = useState('');

  // Data states
  const [logs, setLogs] = useState([]);
  const [filteredLogs, setFilteredLogs] = useState([]);
  const [devices, setDevices] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [refreshTrigger, setRefreshTrigger] = useState(0);

  // Backend pagination states
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [totalLogs, setTotalLogs] = useState(0);
  const [logsPerPage, setLogsPerPage] = useState(20);

  // Sorting state - Using ASC/DESC to match gfe-sphere
  const [sortConfig, setSortConfig] = useState({ key: 'timestamp', direction: 'DESC' });

  // Log type configurations
  const logTypes = [
    { value: 'communication', label: 'Communication', icon: Activity, color: 'from-blue-500 to-cyan-500' },
    { value: 'control', label: 'Control', icon: Settings, color: 'from-[#8c7ae6] to-[#a78bfa]' },
    { value: 'settings', label: 'Settings', icon: Settings, color: 'from-orange-500 to-red-500' },
  ];

  // Sorting icons
  const sortIcons = {
    ArrowUpDown,
    ArrowUp,
    ArrowDown,
  };

  // Handle sorting with API call
  const handleSortWithReset = (key) => {
    const direction = sortConfig.key === key && sortConfig.direction === 'ASC' ? 'DESC' : 'ASC';
    setSortConfig({ key, direction });
    setCurrentPage(1);
    setRefreshTrigger((prev) => prev + 1); // Trigger API call with new sort
  };

  // Handle page change
  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
    setRefreshTrigger((prev) => prev + 1); // Trigger API call with new page
  };

  // Handle limit change
  const handleLimitChange = (newLimit) => {
    setLogsPerPage(parseInt(newLimit, 10));
    setCurrentPage(1); // Reset to first page when changing limit
    setRefreshTrigger((prev) => prev + 1); // Trigger API call with new limit
  };

  // Get log level icon and color
  const getLogLevelIcon = (level) => {
    switch (level?.toUpperCase()) {
      case 'ERROR':
        return { icon: XCircle, color: 'text-red-500' };
      case 'WARN':
      case 'WARNING':
        return { icon: AlertTriangle, color: 'text-yellow-500' };
      case 'INFO':
        return { icon: Info, color: 'text-blue-500' };
      case 'DEBUG':
        return { icon: Settings, color: 'text-gray-500' };
      default:
        return { icon: Info, color: 'text-gray-500' };
    }
  };

  // Single useEffect to handle all data fetching
  useEffect(() => {
    const fetchLogs = async () => {
      // Don't fetch if date range is invalid
      if (dateError) {
        return;
      }

      try {
        setIsLoading(true);

        const filters = {
          type: selectedTypes.length > 0 ? selectedTypes : ['communication', 'control', 'settings'],
          device: selectedDevices.length > 0 ? selectedDevices : 'all',
          limit: logsPerPage,
          page: currentPage,
          sortBy: sortConfig.key,
          sortOrder: sortConfig.direction,
        };

        if (startDateTime) {
          // Format date to match backend log timestamp format: "YYYY-MM-DD HH:MM:SS"
          // Parse DateTimePicker format "YYYY-MM-DDTHH:MM:SS" and convert to "YYYY-MM-DD HH:MM:SS"
          // Treat the selected time as UTC to match how backend parses log timestamps
          const dateStr = startDateTime.replace('T', ' ');
          // Ensure we have seconds (add :00 if missing)
          const parts = dateStr.split(' ');
          if (parts.length === 2) {
            const timePart = parts[1];
            const timeParts = timePart.split(':');
            if (timeParts.length === 2) {
              // Add seconds if missing
              filters.startDate = `${parts[0]} ${timePart}:00`;
            } else {
              filters.startDate = dateStr;
            }
          } else {
            filters.startDate = dateStr;
          }
        }
        if (endDateTime) {
          // Format date to match backend log timestamp format: "YYYY-MM-DD HH:MM:SS"
          const dateStr = endDateTime.replace('T', ' ');
          const parts = dateStr.split(' ');
          if (parts.length === 2) {
            const timePart = parts[1];
            const timeParts = timePart.split(':');
            if (timeParts.length === 2) {
              // Add seconds if missing
              filters.endDate = `${parts[0]} ${timePart}:00`;
            } else {
              filters.endDate = dateStr;
            }
          } else {
            filters.endDate = dateStr;
          }
        }

        const result = await ApiService.getLogs(filters);

        // Handle both 200 and 304 responses properly
        if (result && (result.success || result.status === 304)) {
          const apiLogs = result.logs || [];
          const deviceNames = result.deviceNames || [];

          // Transform API logs to match frontend format
          const transformedLogs = apiLogs.map((log, index) => ({
            id: index + 1,
            timestamp: log.timestamp,
            device: log.deviceName,
            type: log.type,
            level: log.level,
            description: log.message,
            thread: log.thread,
            raw: log.raw,
          }));

          setLogs(transformedLogs);
          setFilteredLogs(transformedLogs);

          // Set pagination data
          setTotalLogs(result.total || 0);
          setTotalPages(result.totalPages || 1);
          setCurrentPage(result.currentPage || 1);

          // Set device names from the same API response
          if (deviceNames.length > 0) {
            setDevices(deviceNames.map((name) => ({ device_name: name })));
          }
        } else {
          toast.error('Failed to fetch logs');
          setLogs([]);
          setFilteredLogs([]);
          setDevices([]);
          setTotalLogs(0);
          setTotalPages(1);
        }
      } catch (error) {
        console.error('Error fetching logs:', error);
        toast.error('Failed to fetch logs');
        setLogs([]);
        setFilteredLogs([]);
        setDevices([]);
        setTotalLogs(0);
        setTotalPages(1);
      } finally {
        setIsLoading(false);
      }
    };

    fetchLogs();
  }, [
    selectedTypes,
    selectedDevices,
    startDateTime,
    endDateTime,
    refreshTrigger,
    sortConfig,
    currentPage,
    logsPerPage,
    dateError, // Include dateError to prevent fetch when invalid
  ]);

  // Validate date range
  const validateDateRange = (start, end) => {
    if (!start || !end) {
      setDateError('');
      return true;
    }

    const startDate = new Date(start);
    const endDate = new Date(end);

    if (isNaN(startDate.getTime()) || isNaN(endDate.getTime())) {
      setDateError('');
      return true; // Let individual date validation handle invalid dates
    }

    if (endDate < startDate) {
      setDateError('End date cannot be earlier than start date');
      return false;
    }

    setDateError('');
    return true;
  };

  // Handle start date change with validation
  const handleStartDateChange = (value) => {
    setStartDateTime(value);
    if (value && endDateTime) {
      validateDateRange(value, endDateTime);
    } else {
      setDateError('');
    }
  };

  // Handle end date change with validation
  const handleEndDateChange = (value) => {
    setEndDateTime(value);
    if (value && startDateTime) {
      validateDateRange(startDateTime, value);
    } else {
      setDateError('');
    }
  };

  // Handle search filtering (client-side for current page)
  useEffect(() => {
    let filtered = [...logs];

    if (searchQuery.trim()) {
      const q = searchQuery.toLowerCase();
      filtered = filtered.filter(
        (log) =>
          log.description.toLowerCase().includes(q) ||
          log.device.toLowerCase().includes(q) ||
          log.level.toLowerCase().includes(q),
      );
    }

    setFilteredLogs(filtered);
  }, [logs, searchQuery]);

  // Clear all filters
  const clearFilters = () => {
    setStartDateTime('');
    setEndDateTime('');
    setSelectedTypes([]);
    setSelectedDevices([]);
    setSearchQuery('');
    setDateError('');
    setLogsPerPage(20); // Reset to default
    setCurrentPage(1); // Reset to first page
  };

  // Handle refresh
  const refreshLogs = () => {
    setRefreshTrigger((prev) => prev + 1);
    toast.success('Logs refreshed successfully!');
  };

  // Backend pagination - no need for client-side slicing
  const currentLogs = filteredLogs;

  // Format timestamp for display with milliseconds
  const formatTimestamp = (timestamp) => {
    try {
      // If timestamp is already in the format "2025-12-11 18:15:25.377", use it directly
      if (typeof timestamp === 'string' && timestamp.match(/^\d{4}-\d{2}-\d{2} \d{2}:\d{2}:\d{2}\.\d{3}$/)) {
        return timestamp;
      }
      
      // Otherwise, parse as Date and format with milliseconds
      const date = new Date(timestamp);
      if (isNaN(date.getTime())) {
        return timestamp; // Return original if invalid
      }
      
      // Format: DD/MM/YYYY, HH:MM:SS.mmm
      const day = String(date.getDate()).padStart(2, '0');
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const year = date.getFullYear();
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      const seconds = String(date.getSeconds()).padStart(2, '0');
      const milliseconds = String(date.getMilliseconds()).padStart(3, '0');
      
      return `${day}/${month}/${year}, ${hours}:${minutes}:${seconds}.${milliseconds}`;
    } catch (error) {
      return timestamp;
    }
  };

  return (
    <>
      <div className="min-h-screen bg-[#f2f4f7] dark:bg-gray-950 p-6 relative overflow-hidden">
        <style jsx>{`
          .table-container {
            transition: opacity 0.2s ease-in-out;
          }
          .table-container.loading {
            opacity: 0.8;
          }
        `}</style>

        {/* Perfect gradient background that merges beautifully in the middle */}
        <div className="absolute inset-0 overflow-hidden pointer-events-none">
          {/* Main gradient overlays */}
          <div className="absolute inset-0 bg-gradient-to-r from-[#8c7ae6]/8 via-[#a78bfa]/12 to-[#8c7ae6]/8" />
          <div className="absolute inset-0 bg-gradient-to-b from-[#8c7ae6]/6 via-[#a78bfa]/10 to-[#8c7ae6]/6" />

          {/* Subtle decorative elements */}
          <div className="absolute top-1/4 right-1/4 w-80 h-80 bg-gradient-to-br from-[#8c7ae6]/5 via-[#a78bfa]/8 to-[#8c7ae6]/5 rounded-full blur-3xl animate-pulse" />
          <div className="absolute bottom-1/3 left-1/3 w-72 h-72 bg-gradient-to-tl from-[#8c7ae6]/6 via-[#a78bfa]/4 to-[#8c7ae6]/6 rounded-full blur-3xl animate-pulse delay-1000" />
        </div>

        {/* Header - Matching gfe-sphere style */}
        <div className="mb-6 relative z-10">
          <div className="bg-white dark:bg-gray-900/70 rounded-xl border border-[#24292e1f] dark:border-gray-800/50 px-4 py-4">
            <div className="flex items-center justify-between">
              <div>
                <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-1">
                  System Logs
                </h1>
                <p className="text-sm text-gray-600 dark:text-gray-300">
                  Monitor and analyze system communication logs
                </p>
              </div>
              <div className="flex items-center gap-2">
                <span className="text-sm text-gray-600 dark:text-gray-300">
                  {totalLogs} {totalLogs === 1 ? 'Log' : 'Logs'}
                </span>
              </div>
            </div>
          </div>
        </div>

        <div className="space-y-8">
          {/* Search and Filters */}
          <div className="mb-8 relative z-10">
            <div className="bg-white dark:bg-gray-900/70  border border-white/30 rounded-lg shadow-xl p-6">
              <div className="space-y-6">
                {/* Search Bar */}
                <div className="relative">
                  <div className="absolute inset-y-0 left-0 pl-4 flex items-center pointer-events-none">
                    <Search className="h-5 w-5 text-[#8c7ae6]" />
                  </div>
                  <input
                    type="text"
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    className="block w-full pl-12 pr-4 py-3 border border-gray-300 rounded-lg text-sm placeholder-gray-500 focus:ring-2 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] bg-white dark:bg-gray-900/70  transition-all duration-200"
                    placeholder="Search logs by description, device, or level..."
                  />
                </div>

                {/* Filters Row */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
                  {/* Log Type Multi-Select Filter */}
                  <div>
                    <MultiSelect
                      label="Log Type"
                      options={logTypes.map((type) => ({
                        value: type.value,
                        label: type.label,
                        icon: type.icon,
                        color: type.color
                      }))}
                      selected={selectedTypes}
                      onChange={setSelectedTypes}
                      placeholder="Select log types..."
                      disabled={isLoading}
                    />
                  </div>

                  {/* Device Multi-Select Filter */}
                  <div>
                    <MultiSelect
                      label="Device"
                      options={devices.map((device) => ({
                        value: device.device_name,
                        label: device.device_name
                      }))}
                      selected={selectedDevices}
                      onChange={setSelectedDevices}
                      placeholder="Select devices..."
                      disabled={isLoading}
                    />
                  </div>

                  {/* Start Date */}
                  <div>
                    <DateTimePicker
                      label="Start Date & Time"
                      value={startDateTime}
                      onChange={(value) => handleStartDateChange(value)}
                      max={endDateTime || undefined}
                      error={dateError && !endDateTime ? dateError : null}
                      placeholder="Select start date & time"
                      showSeconds={true}
                    />
                  </div>

                  {/* End Date */}
                  <div>
                    <DateTimePicker
                      label="End Date & Time"
                      value={endDateTime}
                      onChange={(value) => handleEndDateChange(value)}
                      min={startDateTime || undefined}
                      error={dateError}
                      placeholder="Select end date & time"
                      showSeconds={true}
                    />
                  </div>

                  {/* Results Counter */}
                  <div className="flex items-end">
                    <div className="w-full px-4 py-2.5 bg-gradient-to-r from-[#8c7ae6]/10 to-[#a78bfa]/10 rounded-lg border border-[#8c7ae6]/30">
                      <div className="flex items-center gap-2">
                        <Filter size={16} className="text-[#8c7ae6]" />
                        <span className="text-sm font-medium text-[#8c7ae6]">
                          {filteredLogs.length} of {totalLogs} logs
                        </span>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Clear Filters Button */}
                <div className="mt-4 flex justify-end">
                  <button
                    onClick={clearFilters}
                    className="px-4 py-2.5 text-sm text-gray-600 dark:text-gray-300 hover:text-gray-800 dark:text-white transition-colors duration-200"
                  >
                    Clear All Filters
                  </button>
                </div>
              </div>
            </div>
          </div>

          {/* Logs Table */}
          <div className="bg-white dark:bg-gray-900/70 rounded-xl border border-[#24292e1f] dark:border-gray-800/50 overflow-hidden">
            {filteredLogs.length === 0 && !isLoading ? (
                  <div className="p-8 text-center transition-all duration-500 ease-in-out">
                    <div className="relative">
                      <FileText className="w-16 h-16 text-gray-300 mx-auto mb-4" />
                      <div className="absolute inset-0 w-16 h-16 bg-gradient-to-br from-[#8c7ae6]/10 to-[#a78bfa]/10 rounded-full blur-xl" />
                    </div>
                    <p className="text-gray-600 dark:text-gray-300 font-medium text-lg">No logs found</p>
                    <p className="text-gray-500 text-sm">Try adjusting your filters or refresh the data</p>
                  </div>
                ) : (
                  <div className="relative">
                    {/* Loading Overlay */}
                    {isLoading && (
                      <div className="absolute inset-0 bg-white dark:bg-gray-900/70 z-10 flex items-center justify-center">
                        <Spinner size="lg" />
                      </div>
                    )}

                    {/* Table - Matching gfe-sphere style exactly */}
                    <div className="overflow-x-auto">
                      <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700" style={{ WebkitFontSmoothing: 'antialiased', MozOsxFontSmoothing: 'grayscale', textRendering: 'optimizeLegibility' }}>
                          <thead className="bg-[#f2f4f7] dark:bg-gray-800 sticky top-0 z-10">
                            <tr>
                              <SortableTableHeader
                                label="Date & Time"
                                sortKey="timestamp"
                                currentSortBy={sortConfig.key}
                                currentSortOrder={sortConfig.direction}
                                onSort={handleSortWithReset}
                              />
                              <SortableTableHeader
                                label="Device"
                                sortKey="device"
                                currentSortBy={sortConfig.key}
                                currentSortOrder={sortConfig.direction}
                                onSort={handleSortWithReset}
                              />
                              <SortableTableHeader
                                label="Level"
                                sortKey="level"
                                currentSortBy={sortConfig.key}
                                currentSortOrder={sortConfig.direction}
                                onSort={handleSortWithReset}
                              />
                              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                                Description
                              </th>
                            </tr>
                          </thead>
                          <tbody className="bg-white dark:bg-gray-900/70 divide-y divide-gray-200 dark:divide-gray-700">
                          {currentLogs.length === 0 ? (
                            <tr>
                              <td colSpan={4} className="px-6 py-12 text-center">
                                <div className="flex flex-col items-center gap-3">
                                  <FileText className="w-12 h-12 text-gray-400 dark:text-gray-500" />
                                  <div>
                                    <p className="text-sm font-medium text-gray-900 dark:text-white">No logs found</p>
                                    <p className="text-sm text-gray-600 dark:text-gray-300 mt-1">
                                      Try adjusting your filters or search query
                                    </p>
                                  </div>
                                </div>
                              </td>
                            </tr>
                          ) : (
                            currentLogs.map((log, index) => {
                              const levelInfo = getLogLevelIcon(log.level);
                              const LevelIcon = levelInfo.icon;

                              return (
                                <tr
                                  key={`${log.timestamp}-${log.device}-${index}`}
                                  className="hover:bg-blue-50 dark:hover:bg-gray-700/50 transition-colors"
                                >
                                  <td className="px-6 py-4 whitespace-nowrap">
                                    <div className="text-sm text-gray-600 dark:text-gray-300">
                                      {formatTimestamp(log.timestamp)}
                                    </div>
                                  </td>
                                  <td className="px-6 py-4 whitespace-nowrap">
                                    <div className="text-sm font-semibold text-gray-900 dark:text-white">
                                      {log.device}
                                    </div>
                                  </td>
                                  <td className="px-6 py-4 whitespace-nowrap">
                                    <div className="flex items-center gap-2">
                                      <LevelIcon className={`w-4 h-4 ${levelInfo.color}`} />
                                      <span className={`text-sm font-medium ${levelInfo.color}`}>{log.level}</span>
                                    </div>
                                  </td>
                                  <td className="px-6 py-4">
                                    <div className="text-sm text-gray-600 dark:text-gray-300 max-w-md truncate" title={log.description}>
                                      {log.description}
                                    </div>
                                  </td>
                                </tr>
                              );
                            })
                          )}
                          </tbody>
                        </table>
                    </div>

                    {/* Pagination - Matching gfe-sphere exactly */}
                      <Pagination
                        meta={{
                          total: totalLogs,
                          totalPages: totalPages,
                          hasNextPage: currentPage < totalPages,
                          hasPreviousPage: currentPage > 1,
                        }}
                        page={currentPage}
                        limit={logsPerPage}
                        onPageChange={handlePageChange}
                        onLimitChange={(newLimit) => {
                          setLogsPerPage(newLimit);
                          setCurrentPage(1);
                        }}
                        limitOptions={[20, 30, 50, 100]}
                        showLimitSelector={true}
                      />
                  </div>
                )}
          </div>
        </div>
      </div>
    </>
  );
};

export default Logs;
