import React from 'react';
import Spinner from './Spinner';

const FullScreenLoader = ({ isVisible, message, type = 'login' }) => {
  if (!isVisible) return null;

  return (
    <div className="fixed inset-0 z-[9999999] flex items-center justify-center bg-black/15 backdrop-blur-xl">
      <Spinner size="lg" />
    </div>
  );
};

export default FullScreenLoader;
