import React, { useState, useRef, useEffect } from 'react';
import { createPortal } from 'react-dom';
import { Calendar, Clock, ChevronLeft, ChevronRight, X } from 'lucide-react';

const DateTimePicker = ({
  value = '',
  onChange,
  label,
  placeholder = 'Select date and time',
  min,
  max,
  error,
  disabled = false,
  className = '',
  showTime = true,
  showSeconds = false,
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [viewDate, setViewDate] = useState(new Date());
  const [selectedDate, setSelectedDate] = useState(value ? new Date(value) : null);
  const [selectedTime, setSelectedTime] = useState({ hours: 0, minutes: 0, seconds: 0 });
  const [timeInputs, setTimeInputs] = useState({ hours: '0', minutes: '0', seconds: '0' });
  const [pickerPosition, setPickerPosition] = useState({ top: 0, left: 0, width: 0 });
  const containerRef = useRef(null);
  const pickerRef = useRef(null);

  // Parse initial value
  useEffect(() => {
    if (value) {
      const date = new Date(value);
      if (!isNaN(date.getTime())) {
        setSelectedDate(date);
        const hours = date.getHours();
        const minutes = date.getMinutes();
        const seconds = date.getSeconds();
        setSelectedTime({ hours, minutes, seconds });
        setTimeInputs({ 
          hours: String(hours), 
          minutes: String(minutes), 
          seconds: String(seconds) 
        });
        setViewDate(date);
      }
    } else {
      setSelectedDate(null);
      setSelectedTime({ hours: 0, minutes: 0, seconds: 0 });
      setTimeInputs({ hours: '0', minutes: '0', seconds: '0' });
    }
  }, [value]);

  // Calculate picker position
  useEffect(() => {
    if (isOpen && containerRef.current) {
      const rect = containerRef.current.getBoundingClientRect();
      const viewportHeight = window.innerHeight;
      const viewportWidth = window.innerWidth;
      const pickerHeight = showTime ? 380 : 280;
      
      const spaceBelow = viewportHeight - rect.bottom;
      const spaceAbove = rect.top;
      const openAbove = spaceBelow < pickerHeight && spaceAbove > spaceBelow;
      
      let left = rect.left + window.scrollX;
      const pickerWidth = 340;
      if (left + pickerWidth > viewportWidth + window.scrollX) {
        left = viewportWidth + window.scrollX - pickerWidth - 10;
      }
      if (left < window.scrollX) {
        left = window.scrollX + 10;
      }
      
      setPickerPosition({
        top: openAbove 
          ? rect.top + window.scrollY - pickerHeight - 4
          : rect.bottom + window.scrollY + 4,
        left: left,
        width: rect.width
      });
    }
  }, [isOpen, showTime]);

  // Update position on scroll/resize
  useEffect(() => {
    if (!isOpen) return;

    const updatePosition = () => {
      if (containerRef.current) {
        const rect = containerRef.current.getBoundingClientRect();
        const viewportHeight = window.innerHeight;
        const viewportWidth = window.innerWidth;
        const pickerHeight = showTime ? 380 : 280;
        
        const spaceBelow = viewportHeight - rect.bottom;
        const spaceAbove = rect.top;
        const openAbove = spaceBelow < pickerHeight && spaceAbove > spaceBelow;
        
        let left = rect.left + window.scrollX;
        const pickerWidth = 340;
        if (left + pickerWidth > viewportWidth + window.scrollX) {
          left = viewportWidth + window.scrollX - pickerWidth - 10;
        }
        if (left < window.scrollX) {
          left = window.scrollX + 10;
        }
        
        setPickerPosition({
          top: openAbove 
            ? rect.top + window.scrollY - pickerHeight - 4
            : rect.bottom + window.scrollY + 4,
          left: left,
          width: rect.width
        });
      }
    };

    window.addEventListener('scroll', updatePosition, true);
    window.addEventListener('resize', updatePosition);

    return () => {
      window.removeEventListener('scroll', updatePosition, true);
      window.removeEventListener('resize', updatePosition);
    };
  }, [isOpen, showTime]);

  // Close on outside click
  useEffect(() => {
    if (!isOpen) return;

    const handleClickOutside = (event) => {
      const target = event.target;
      if (
        containerRef.current && 
        !containerRef.current.contains(target) &&
        !target.closest('[data-datetime-picker]')
      ) {
        setIsOpen(false);
      }
    };

    const timeoutId = setTimeout(() => {
      document.addEventListener('mousedown', handleClickOutside);
    }, 100);

    return () => {
      clearTimeout(timeoutId);
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen]);

  const formatDisplayValue = () => {
    if (!selectedDate) return '';
    const date = selectedDate;
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    const hours = String(selectedTime.hours).padStart(2, '0');
    const minutes = String(selectedTime.minutes).padStart(2, '0');
    const seconds = showSeconds ? `:${String(selectedTime.seconds).padStart(2, '0')}` : '';
    return `${year}-${month}-${day} ${hours}:${minutes}${seconds}`;
  };

  const formatValueForInput = () => {
    if (!selectedDate) return '';
    const date = selectedDate;
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    const hours = String(selectedTime.hours).padStart(2, '0');
    const minutes = String(selectedTime.minutes).padStart(2, '0');
    // For datetime-local format, we use HH:MM format (seconds not supported in HTML5 datetime-local)
    // But we can still include seconds in the value if showSeconds is true for internal use
    if (showSeconds) {
      const seconds = String(selectedTime.seconds).padStart(2, '0');
      return `${year}-${month}-${day}T${hours}:${minutes}:${seconds}`;
    }
    return `${year}-${month}-${day}T${hours}:${minutes}`;
  };

  const handleDateSelect = (day) => {
    const newDate = new Date(viewDate.getFullYear(), viewDate.getMonth(), day);
    setSelectedDate(newDate);
  };

  const handleTimeInputChange = (type, value) => {
    // Allow empty string during editing
    setTimeInputs(prev => ({
      ...prev,
      [type]: value === '' ? '' : value
    }));

    // Only update selectedTime if we have a valid number
    const numValue = value === '' ? null : parseInt(value);
    if (numValue !== null && !isNaN(numValue)) {
      setSelectedTime(prev => {
        const newTime = { ...prev };
        if (type === 'hours') {
          newTime.hours = Math.max(0, Math.min(23, numValue));
          setTimeInputs(prevInputs => ({
            ...prevInputs,
            hours: String(Math.max(0, Math.min(23, numValue)))
          }));
        } else if (type === 'minutes') {
          newTime.minutes = Math.max(0, Math.min(59, numValue));
          setTimeInputs(prevInputs => ({
            ...prevInputs,
            minutes: String(Math.max(0, Math.min(59, numValue)))
          }));
        } else if (type === 'seconds') {
          newTime.seconds = Math.max(0, Math.min(59, numValue));
          setTimeInputs(prevInputs => ({
            ...prevInputs,
            seconds: String(Math.max(0, Math.min(59, numValue)))
          }));
        }
        return newTime;
      });
    }
  };

  const handleTimeBlur = (type) => {
    // On blur, ensure we have a valid value
    const currentValue = timeInputs[type];
    const numValue = currentValue === '' ? 0 : parseInt(currentValue);
    const validValue = isNaN(numValue) ? 0 : numValue;
    
    let clampedValue = validValue;
    if (type === 'hours') {
      clampedValue = Math.max(0, Math.min(23, validValue));
    } else if (type === 'minutes' || type === 'seconds') {
      clampedValue = Math.max(0, Math.min(59, validValue));
    }

    setTimeInputs(prev => ({
      ...prev,
      [type]: String(clampedValue)
    }));

    setSelectedTime(prev => ({
      ...prev,
      [type]: clampedValue
    }));
  };

  const handleApply = () => {
    if (selectedDate) {
      const dateTime = new Date(selectedDate);
      dateTime.setHours(selectedTime.hours, selectedTime.minutes, selectedTime.seconds, 0);
      const formatted = formatValueForInput();
      onChange(formatted);
      setIsOpen(false);
    }
  };

  const handleClear = () => {
    setSelectedDate(null);
    setSelectedTime({ hours: 0, minutes: 0, seconds: 0 });
    setTimeInputs({ hours: '0', minutes: '0', seconds: '0' });
    onChange('');
    setIsOpen(false);
  };

  const handleToday = () => {
    const today = new Date();
    setSelectedDate(today);
    setViewDate(today);
    const hours = today.getHours();
    const minutes = today.getMinutes();
    const seconds = today.getSeconds();
    setSelectedTime({ hours, minutes, seconds });
    setTimeInputs({ 
      hours: String(hours), 
      minutes: String(minutes), 
      seconds: String(seconds) 
    });
  };

  const navigateMonth = (direction) => {
    setViewDate(prev => {
      const newDate = new Date(prev);
      newDate.setMonth(prev.getMonth() + direction);
      return newDate;
    });
  };

  const navigateYear = (direction) => {
    setViewDate(prev => {
      const newDate = new Date(prev);
      newDate.setFullYear(prev.getFullYear() + direction);
      return newDate;
    });
  };

  const getDaysInMonth = (date) => {
    return new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate();
  };

  const getFirstDayOfMonth = (date) => {
    return new Date(date.getFullYear(), date.getMonth(), 1).getDay();
  };

  const isDateDisabled = (day) => {
    if (!min && !max) return false;
    const checkDate = new Date(viewDate.getFullYear(), viewDate.getMonth(), day);
    if (min) {
      const minDate = new Date(min);
      if (checkDate < minDate) return true;
    }
    if (max) {
      const maxDate = new Date(max);
      if (checkDate > maxDate) return true;
    }
    return false;
  };

  const isToday = (day) => {
    const today = new Date();
    return (
      day === today.getDate() &&
      viewDate.getMonth() === today.getMonth() &&
      viewDate.getFullYear() === today.getFullYear()
    );
  };

  const isSelected = (day) => {
    if (!selectedDate) return false;
    return (
      day === selectedDate.getDate() &&
      viewDate.getMonth() === selectedDate.getMonth() &&
      viewDate.getFullYear() === selectedDate.getFullYear()
    );
  };

  const monthNames = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
  const dayNames = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
  const daysInMonth = getDaysInMonth(viewDate);
  const firstDay = getFirstDayOfMonth(viewDate);
  const days = [];

  // Empty cells for days before the first day of the month
  for (let i = 0; i < firstDay; i++) {
    days.push(null);
  }

  // Days of the month
  for (let day = 1; day <= daysInMonth; day++) {
    days.push(day);
  }

  // Fill remaining cells to always show 6 rows (42 cells) for consistent layout
  // This prevents gaps when navigating between months
  const totalCells = 42; // 6 rows  7 days
  while (days.length < totalCells) {
    days.push(null);
  }

  return (
    <>
      <div className={`relative ${className}`} ref={containerRef}>
        {label && (
          <label className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-2">
            {label}
          </label>
        )}
        
        <div
          onClick={() => !disabled && setIsOpen(!isOpen)}
          className={`
            w-full border rounded-lg px-4 py-2.5 text-sm 
            focus:ring-2 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] 
            bg-white dark:bg-gray-900/70
            cursor-pointer transition-all duration-200
            flex items-center gap-3
            ${disabled ? 'opacity-50 cursor-not-allowed' : 'hover:border-[#8c7ae6]/50 hover:shadow-sm'}
            ${isOpen ? 'border-[#8c7ae6] ring-2 ring-[#8c7ae6]/30 shadow-md' : error ? 'border-red-500' : 'border-gray-300 dark:border-gray-600'}
          `}
        >
          <Calendar className="w-5 h-5 text-[#8c7ae6] dark:text-[#a78bfa] flex-shrink-0" />
          <span className={`flex-1 ${value ? 'text-gray-900 dark:text-white' : 'text-gray-500 dark:text-gray-400'}`}>
            {value ? formatDisplayValue() : placeholder}
          </span>
          {value && !disabled && (
            <button
              type="button"
              onClick={(e) => {
                e.stopPropagation();
                handleClear();
              }}
              className="p-1 hover:bg-gray-100 dark:hover:bg-gray-700 rounded transition-colors"
            >
              <X className="w-4 h-4 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300" />
            </button>
          )}
        </div>
        
        {error && (
          <p className="mt-1 text-sm text-red-500">{error}</p>
        )}
      </div>

      {isOpen && !disabled && createPortal(
        <div
          data-datetime-picker
          ref={pickerRef}
          className="fixed z-[99999] bg-white dark:bg-gray-900 border border-[#8c7ae6]/20 dark:border-[#8c7ae6]/30 rounded-xl shadow-2xl overflow-hidden backdrop-blur-sm"
          style={{
            top: `${pickerPosition.top}px`,
            left: `${pickerPosition.left}px`,
            width: '340px',
          }}
        >
          {/* Header with gradient */}
          <div className="bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] px-3 py-2.5 text-white">
            <div className="flex items-center justify-between">
              <button
                type="button"
                onClick={() => navigateYear(-1)}
                className="p-1 hover:bg-white/20 rounded transition-colors"
                title="Previous year"
              >
                <ChevronLeft className="w-4 h-4" />
              </button>
              <div className="flex items-center gap-1">
                <button
                  type="button"
                  onClick={() => navigateMonth(-1)}
                  className="p-1 hover:bg-white/20 rounded transition-colors"
                  title="Previous month"
                >
                  <ChevronLeft className="w-3.5 h-3.5" />
                </button>
                <h3 className="text-sm font-bold px-3 min-w-[140px] text-center">
                  {monthNames[viewDate.getMonth()].substring(0, 3)} {viewDate.getFullYear()}
                </h3>
                <button
                  type="button"
                  onClick={() => navigateMonth(1)}
                  className="p-1 hover:bg-white/20 rounded transition-colors"
                  title="Next month"
                >
                  <ChevronRight className="w-3.5 h-3.5" />
                </button>
              </div>
              <div className="flex items-center gap-1">
                <button
                  type="button"
                  onClick={handleToday}
                  className="text-xs px-2 py-1 bg-white/20 hover:bg-white/30 rounded transition-colors font-medium"
                  title="Go to today"
                >
                  Today
                </button>
                <button
                  type="button"
                  onClick={() => navigateYear(1)}
                  className="p-1 hover:bg-white/20 rounded transition-colors"
                  title="Next year"
                >
                  <ChevronRight className="w-4 h-4" />
                </button>
              </div>
            </div>
          </div>

          {/* Calendar Grid */}
          <div className="p-3">
            {/* Day names */}
            <div className="grid grid-cols-7 gap-0.5 mb-1.5">
              {dayNames.map(day => (
                <div key={day} className="text-center text-[10px] font-semibold text-gray-500 dark:text-gray-400 py-1">
                  {day.substring(0, 1)}
                </div>
              ))}
            </div>

            {/* Calendar days - Always 6 rows for consistent spacing */}
            <div className="grid grid-cols-7 gap-0.5 min-h-[168px]">
              {days.map((day, index) => {
                if (day === null) {
                  return <div key={`empty-${index}`} className="h-7 w-full flex-shrink-0" />;
                }
                
                const disabled = isDateDisabled(day);
                const today = isToday(day);
                const selected = isSelected(day);
                
                return (
                  <button
                    key={day}
                    type="button"
                    onClick={() => !disabled && handleDateSelect(day)}
                    disabled={disabled}
                    className={`
                      h-7 w-full rounded-md text-xs font-medium transition-all duration-150 flex items-center justify-center
                      ${disabled 
                        ? 'text-gray-300 dark:text-gray-600 cursor-not-allowed' 
                        : selected
                          ? 'bg-gradient-to-br from-[#8c7ae6] to-[#a78bfa] text-white shadow-md shadow-[#8c7ae6]/25 scale-105'
                          : today
                            ? 'bg-gradient-to-br from-[#8c7ae6]/15 to-[#a78bfa]/15 text-[#8c7ae6] dark:text-[#a78bfa] border border-[#8c7ae6]/40 font-semibold'
                            : 'text-gray-700 dark:text-gray-300 hover:bg-gradient-to-br hover:from-[#8c7ae6]/10 hover:to-[#a78bfa]/10'
                      }
                    `}
                  >
                    {day}
                  </button>
                );
              })}
            </div>
          </div>

          {/* Time Picker */}
          {showTime && (
            <div className="border-t border-gray-200 dark:border-gray-700 px-3 py-2.5 bg-gradient-to-r from-[#8c7ae6]/5 to-[#a78bfa]/5">
              <div className="flex items-center gap-1.5 mb-2">
                <Clock className="w-3.5 h-3.5 text-[#8c7ae6] dark:text-[#a78bfa]" />
                <span className="text-xs font-semibold text-gray-700 dark:text-gray-300">Time</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="flex-1">
                  <label className="text-[10px] text-gray-500 dark:text-gray-400 mb-0.5 block font-medium">H</label>
                  <input
                    type="text"
                    inputMode="numeric"
                    pattern="[0-9]*"
                    value={timeInputs.hours}
                    onChange={(e) => handleTimeInputChange('hours', e.target.value.replace(/[^0-9]/g, ''))}
                    onBlur={() => handleTimeBlur('hours')}
                    className="w-full px-2 py-1.5 border border-gray-300 dark:border-gray-600 rounded-md bg-white dark:bg-gray-800 text-center text-xs font-semibold focus:ring-1 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] transition-all"
                  />
                </div>
                <div className="pt-4 text-gray-400 dark:text-gray-500 font-bold text-sm">:</div>
                <div className="flex-1">
                  <label className="text-[10px] text-gray-500 dark:text-gray-400 mb-0.5 block font-medium">M</label>
                  <input
                    type="text"
                    inputMode="numeric"
                    pattern="[0-9]*"
                    value={timeInputs.minutes}
                    onChange={(e) => handleTimeInputChange('minutes', e.target.value.replace(/[^0-9]/g, ''))}
                    onBlur={() => handleTimeBlur('minutes')}
                    className="w-full px-2 py-1.5 border border-gray-300 dark:border-gray-600 rounded-md bg-white dark:bg-gray-800 text-center text-xs font-semibold focus:ring-1 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] transition-all"
                  />
                </div>
                {showSeconds && (
                  <>
                    <div className="pt-4 text-gray-400 dark:text-gray-500 font-bold text-sm">:</div>
                    <div className="flex-1">
                      <label className="text-[10px] text-gray-500 dark:text-gray-400 mb-0.5 block font-medium">S</label>
                      <input
                        type="text"
                        inputMode="numeric"
                        pattern="[0-9]*"
                        value={timeInputs.seconds}
                        onChange={(e) => handleTimeInputChange('seconds', e.target.value.replace(/[^0-9]/g, ''))}
                        onBlur={() => handleTimeBlur('seconds')}
                        className="w-full px-2 py-1.5 border border-gray-300 dark:border-gray-600 rounded-md bg-white dark:bg-gray-800 text-center text-xs font-semibold focus:ring-1 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] transition-all"
                      />
                    </div>
                  </>
                )}
              </div>
            </div>
          )}

          {/* Footer Actions */}
          <div className="border-t border-gray-200 dark:border-gray-700 px-3 py-2 bg-gradient-to-r from-[#8c7ae6]/5 to-[#a78bfa]/5 flex items-center justify-between gap-2">
            <button
              type="button"
              onClick={handleClear}
              className="px-3 py-1.5 text-xs text-gray-600 dark:text-gray-300 hover:text-gray-800 dark:hover:text-white transition-colors font-medium rounded-md hover:bg-gray-100 dark:hover:bg-gray-800"
            >
              Clear
            </button>
            <button
              type="button"
              onClick={handleApply}
              disabled={!selectedDate}
              className="px-4 py-1.5 bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] text-white rounded-md font-semibold text-xs hover:shadow-md hover:shadow-[#8c7ae6]/30 transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Apply
            </button>
          </div>
        </div>
      , document.body)}
    </>
  );
};

export default DateTimePicker;

