import React, { createContext, useContext, useState } from 'react';

const ConnectionStatusContext = createContext();

export const useConnectionStatus = () => {
  const context = useContext(ConnectionStatusContext);
  if (!context) {
    throw new Error('useConnectionStatus must be used within ConnectionStatusProvider');
  }
  return context;
};

export const ConnectionStatusProvider = ({ children }) => {
  const [isReachable, setIsReachable] = useState(true);

  const setReachable = (status) => {
    setIsReachable(status);
  };

  return (
    <ConnectionStatusContext.Provider value={{ isReachable, setReachable }}>
      {children}
    </ConnectionStatusContext.Provider>
  );
};

