import React, { useState, useEffect } from 'react';
import { createPortal } from 'react-dom';
import { X, Wifi, Eye, EyeOff, CheckCircle, XCircle, AlertCircle, WifiOff, RefreshCw } from 'lucide-react';
import { toast } from 'react-toastify';
import ApiService from '../services/apiService';

const WifiSettingsModal = ({ isOpen, onClose }) => {
  const [networks, setNetworks] = useState([]);
  const [selectedSSID, setSelectedSSID] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [isScanning, setIsScanning] = useState(false);
  const [isConnecting, setIsConnecting] = useState(false);
  const [wifiStatus, setWifiStatus] = useState(null);
  const [isLoadingWifiStatus, setIsLoadingWifiStatus] = useState(false);
  const [scanError, setScanError] = useState('');
  const [connectError, setConnectError] = useState('');


  // Load initial data
  useEffect(() => {
    if (isOpen) {
      scanNetworks();
      getWifiStatus(true); // Pass true for initial load
    }
  }, [isOpen]);



  // Auto-poll WiFi status every 10 seconds when modal is open
  useEffect(() => {
    let intervalId

    if (isOpen) {
      intervalId = setInterval(() => {
        getWifiStatus();
      }, 10000);
    }

    return () => {
      if (intervalId) clearInterval(intervalId);
    };
  }, [isOpen]);

  // Scan for nearby networks
  const scanNetworks = async () => {
    setIsScanning(true);
    setScanError('');

    try {
      const result = await ApiService.getNearbyWifiNetworks();
      if (result.ok) {
        const list = result.networks || [];
        setNetworks(list);
        if (list.length === 0) {
          setScanError('No networks found. Make sure WiFi is enabled.');
        }
      } else {
        throw new Error(result.error || 'Failed to scan networks');
      }
    } catch (error) {
      console.error('Scan networks error:', error);
      const errorMessage = error.message || 'Failed to scan networks';
      setScanError(errorMessage);

      if (errorMessage.includes('busy')) {
        toast.error('WiFi interface is busy. Please disconnect first, then try scanning.');
      } else if (errorMessage.includes('No networks found')) {
        toast.error('No WiFi networks found. Make sure WiFi is enabled.');
      } else {
        toast.error('Failed to scan for networks');
      }
    } finally {
      setIsScanning(false);
    }
  };


  // Get current WiFi status
  const getWifiStatus = async (isInitialLoad = false) => {
    if (isInitialLoad) {
      setIsLoadingWifiStatus(true);
    }
    
    try {
      const result = await ApiService.getWifiStatus();
      if (result.ok) {
        setWifiStatus(result);
      } else {
        throw new Error(result.error || 'Failed to get status');
      }
    } catch (error) {
      console.error('Get status error:', error);
      // Only show error on initial load, silent on auto-poll failures
      if (isInitialLoad) {
        setWifiStatus(null);
      }
    } finally {
      if (isInitialLoad) {
        setIsLoadingWifiStatus(false);
      }
    }
  };

  // Connect to selected network
  const connectToNetwork = async () => {
    if (!selectedSSID) {
      toast.error('Please select a network');
      return;
    }

    setIsConnecting(true);
    setConnectError('');

    try {
      const result = await ApiService.connectToWifi(selectedSSID, password);
      if (result.ok) {
        toast.success(`Successfully connected to ${result.connectedTo}`);
        setConnectError('');
        setSelectedSSID('');
        setPassword('');
        setShowPassword(false);
        setTimeout(() => {
          getWifiStatus();
        }, 2000);
      } else {
        throw new Error(result.error || 'Connection failed');
      }
    } catch (error) {
      console.error('Connect error:', error);

      if (error.code === 'WRONG_PASSWORD') {
        setConnectError('Incorrect password. Please check your password and try again.');
      } else if (error.code === 'WPA_CONTROL_UNAVAILABLE') {
        setConnectError('Wi-Fi service unavailable. Please try again.');
      } else if (error.code === 'DHCP_FAILED') {
        setConnectError('Connected to access point but failed to obtain IP address. Connection reverted.');
      } else {
        setConnectError(error.message || 'Failed to connect to network');
      }
    } finally {
      setIsConnecting(false);
    }
  };

  // Disconnect from WiFi
  const disconnectFromWifi = async () => {
    
    setIsConnecting(true);
    setConnectError('');

    try {
      const result = await ApiService.disconnectFromWifi();
      if (result.ok) {
        setConnectError('');
        // Update status immediately for better UX
        setWifiStatus({
          ok: true,
          ssid: null,
          ip: null,
          cidr: null,
          subnet: null,
          gateway: null,
          dns: []
        });
        
        
        // Refresh status after a short delay to confirm
        setTimeout(() => {
          getWifiStatus();
        }, 1000);
      } else {
        throw new Error(result.error || 'Disconnect failed');
      }
    } catch (error) {
      console.error('Disconnect error:', error);
      setConnectError(error.message || 'Failed to disconnect from WiFi');
      toast.error('Failed to disconnect from WiFi. Please try again.');
    } finally {
      setIsConnecting(false);
    }
  };

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    connectToNetwork();
  };

  // Handle modal close
  const handleClose = () => {
    setSelectedSSID('');
    setPassword('');
    setShowPassword(false);
    setScanError('');
    setConnectError('');
    setWifiStatus(null);
    setIsLoadingWifiStatus(false);
    onClose();
  };

  if (!isOpen) return null;

  // ---- NEW: Strong "connected" definition (SSID required) ----
  const hasSSID = Boolean(String(wifiStatus?.ssid || '').trim());
  const hasIP = Boolean(String(wifiStatus?.ip || '').trim());
  const isConnectedNow = hasSSID && hasIP;

  return createPortal(
    <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50 p-4">
      <div className="bg-white dark:bg-gray-900/70 backdrop-blur-xl rounded-3xl shadow-2xl w-full max-w-lg max-h-[85vh] overflow-hidden border border-green-200/40 relative">
        {/* Gradient background */}
        <div className="absolute inset-0 bg-gradient-to-r from-[#8c7ae6]/5 via-[#a78bfa]/8 to-[#8c7ae6]/5"></div>

        {/* Header */}
        <div className="bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] p-6 text-white relative">
          <div className="absolute inset-0 bg-gradient-to-r from-white/10 to-transparent"></div>
          <div className="relative flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-white dark:bg-gray-900/70 rounded-lg flex items-center justify-center backdrop-blur-sm">
                <Wifi className="w-5 h-5" />
              </div>
              <div>
                <h2 className="text-xl font-bold">WiFi Settings</h2>
                <p className="text-white/90 text-sm">Manage your wireless connection</p>
              </div>
            </div>
            <button
              onClick={handleClose}
              className="text-white/80 hover:text-white transition-all duration-200 p-2 rounded-lg hover:bg-white dark:bg-gray-900/70 backdrop-blur-sm"
            >
              <X className="w-5 h-5" />
            </button>
          </div>
        </div>

        <div className="relative z-10 p-6 overflow-y-auto max-h-[calc(85vh-120px)]">
          {/* Current Status */}
          <div className="mb-6">
            <div className="mb-4">
              <div className="flex items-center justify-between">
                <div>
                  <h3 className="text-lg font-bold bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] bg-clip-text text-transparent">
                    Current Status
                  </h3>
                  <div className="h-1 w-20 bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] rounded-full mt-1"></div>
                </div>
                {/* Disconnect Button - Only show when connected */}
                {isConnectedNow && (
                  <button
                    onClick={disconnectFromWifi}
                    disabled={isConnecting}
                    className="flex items-center gap-2 px-4 py-2.5 bg-gradient-to-r from-red-500 to-orange-500 text-white rounded-lg hover:from-red-600 hover:to-orange-600 transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed shadow-md hover:shadow-lg text-sm font-semibold"
                  >
                    {isConnecting ? (
                      <>
                        <RefreshCw className="w-4 h-4 animate-spin" />
                        Disconnecting...
                      </>
                    ) : (
                      <>
                        <WifiOff className="w-4 h-4" />
                        Disconnect
                      </>
                    )}
                  </button>
                )}
              </div>
            </div>

            {isLoadingWifiStatus ? (
              <div className="p-4 bg-blue-50 rounded-lg border-2 border-blue-200">
                <div className="flex items-center gap-3">
                  <div className="w-5 h-5 border-2 border-blue-600 border-t-transparent rounded-full animate-spin"></div>
                  <span className="text-blue-700 font-medium">Loading WiFi status...</span>
                </div>
              </div>
            ) : wifiStatus ? (
              <div
                className={`p-3 rounded-lg border-2 transition-all duration-300 shadow-sm ${
                  isConnectedNow ? 'bg-white dark:bg-gray-900/70 border-green-200' : 'bg-gradient-to-br from-gray-50 to-slate-50 border-gray-200 dark:border-gray-700'
                }`}
              >
                <div className="flex items-center gap-3 mb-3">
                  <div className={`p-1.5 rounded-full ${isConnectedNow ? 'bg-green-100' : 'bg-gray-100'}`}>
                    {isConnectedNow ? (
                      <CheckCircle className="w-4 h-4 text-green-600" />
                    ) : (
                      <WifiOff className="w-4 h-4 text-gray-500 dark:text-gray-300" />
                    )}
                  </div>
                  <div>
                    <span className={`text-sm font-bold ${isConnectedNow ? 'text-green-800' : 'text-gray-600 dark:text-gray-300'}`}>
                      {isConnectedNow ? 'Connected' : 'Not Connected'}
                    </span>
                    {isConnectedNow && <p className="text-xs text-green-600 font-medium">WiFi connection active</p>}
                  </div>
                </div>

                {isConnectedNow && (
                  <div className="space-y-2">
                    <div className="flex justify-between items-center py-1.5 px-2.5 bg-white dark:bg-gray-900/70 rounded-lg text-xs">
                      <span className="text-gray-600 dark:text-gray-300 font-medium">Network:</span>
                      <span className="font-bold text-gray-800 dark:text-white">{wifiStatus.ssid || 'Unknown'}</span>
                    </div>
                    <div className="flex justify-between items-center py-1.5 px-2.5 bg-white dark:bg-gray-900/70 rounded-lg text-xs">
                      <span className="text-gray-600 dark:text-gray-300 font-medium">IP Address:</span>
                      <span className="font-bold text-gray-800 dark:text-white font-mono">{wifiStatus.ip}</span>
                    </div>
                    <div className="flex justify-between items-center py-1.5 px-2.5 bg-white dark:bg-gray-900/70 rounded-lg text-xs">
                      <span className="text-gray-600 dark:text-gray-300 font-medium">Subnet Mask:</span>
                      <span className="font-bold text-gray-800 dark:text-white font-mono">{wifiStatus.subnet || 'N/A'}</span>
                    </div>
                    <div className="flex justify-between items-center py-1.5 px-2.5 bg-white dark:bg-gray-900/70 rounded-lg text-xs">
                      <span className="text-gray-600 dark:text-gray-300 font-medium">Gateway:</span>
                      <span className="font-bold text-gray-800 dark:text-white font-mono">{wifiStatus.gateway || 'N/A'}</span>
                    </div>
                    <div className="flex justify-between items-center py-1.5 px-2.5 bg-white dark:bg-gray-900/70 rounded-lg text-xs">
                      <span className="text-gray-600 dark:text-gray-300 font-medium">DNS Servers:</span>
                      <span className="font-bold text-gray-800 dark:text-white font-mono">
                        {wifiStatus.dns && wifiStatus.dns.length > 0 ? wifiStatus.dns.join(', ') : 'N/A'}
                      </span>
                    </div>
                  </div>
                )}
              </div>
            ) : (
              <div className="p-4 bg-gray-50 rounded-lg border-2 border-gray-200 dark:border-gray-700">
                <div className="flex items-center gap-3">
                  <AlertCircle className="w-5 h-5 text-gray-500 dark:text-gray-300" />
                  <span className="text-gray-600 dark:text-gray-300">Status unavailable</span>
                </div>
              </div>
            )}

          </div>

          {/* Network Selection */}
          <div className="mb-6">
            <div className="flex items-center justify-between mb-4">
              <div>
                <h3 className="text-lg font-bold bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] bg-clip-text text-transparent">
                  Available Networks
                </h3>
                <div className="h-1 w-24 bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] rounded-full mt-1"></div>
              </div>
              <button
                onClick={scanNetworks}
                disabled={isScanning}
                className="flex items-center gap-2 px-4 py-2.5 bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] text-white rounded-lg hover:from-[#007a93] hover:to-[#147015] transition-all duration-200 disabled:opacity-50 shadow-md hover:shadow-lg"
              >
                <Wifi className={`w-4 h-4 ${isScanning ? 'animate-pulse' : ''}`} />
                <span className="text-sm font-medium">{isScanning ? 'Scanning...' : 'Scan Networks'}</span>
              </button>
            </div>

            {scanError ? (
              <div className="p-4 bg-red-50 border-2 border-red-200 rounded-lg">
                <div className="flex items-center gap-3">
                  <XCircle className="w-5 h-5 text-red-600" />
                  <span className="text-red-800">{scanError}</span>
                </div>
              </div>
            ) : networks.length > 0 ? (
              <div className="bg-white dark:bg-gray-900/70 backdrop-blur-sm rounded-lg border border-gray-200 dark:border-gray-700 shadow-sm">
                <div className="max-h-48 overflow-y-auto p-2 space-y-2">
                  {networks.map((network, index) => {
                    const connectedSSID = String(wifiStatus?.ssid || '').trim();
                    const isConnectedToThis = isConnectedNow && connectedSSID === network;
                    const isSelected = selectedSSID === network;

                    return (
                      <button
                        key={index}
                        onClick={() => {
                          if (!isConnectedToThis) {
                            setSelectedSSID(network);
                            if (connectError) setConnectError('');
                          }
                        }}
                        disabled={isConnectedToThis}
                        className={`w-full p-3 text-left rounded-lg border-2 transition-all duration-200 ${
                          isConnectedToThis
                            ? 'border-green-200 bg-gradient-to-r from-green-50 to-emerald-50 cursor-not-allowed opacity-75'
                            : isSelected
                            ? 'border-[#8c7ae6] bg-gradient-to-r from-[#8c7ae6]/10 to-[#a78bfa]/10 shadow-md'
                            : 'border-gray-200 dark:border-gray-700 hover:border-gray-300 hover:bg-gradient-to-r hover:from-gray-50 hover:to-slate-50 hover:shadow-sm'
                        }`}
                      >
                        <div className="flex items-center gap-3">
                          <div className={`p-2 rounded-full ${isConnectedToThis ? 'bg-green-100' : 'bg-gray-100'}`}>
                            <Wifi className={`w-4 h-4 ${isConnectedToThis ? 'text-green-600' : 'text-gray-600 dark:text-gray-300'}`} />
                          </div>
                          <div className="flex-1">
                            <div className="flex items-center gap-2">
                              <span className={`text-sm font-semibold ${isConnectedToThis ? 'text-green-800 dark:text-green-300' : 'text-gray-800 dark:text-white'}`}>
                                {network}
                              </span>
                            </div>
                            
                            {/* Connection Status */}
                            {isConnectedToThis && <p className="text-xs text-green-600 font-medium">Currently connected</p>}
                          </div>
                          {isSelected && !isConnectedToThis && <CheckCircle className="w-5 h-5 text-[#8c7ae6]" />}
                          {isConnectedToThis && <CheckCircle className="w-5 h-5 text-green-600" />}
                        </div>
                      </button>
                    );
                  })}
                </div>
              </div>
            ) : (
              <div className="p-4 bg-gradient-to-br from-gray-50 to-slate-50 border-2 border-gray-200 dark:border-gray-700 rounded-lg text-center">
                <div className="flex flex-col items-center gap-2">
                  <div className="p-2 bg-gray-100 rounded-full">
                    <WifiOff className="w-4 h-4 text-gray-500 dark:text-gray-300" />
                  </div>
                  <div>
                    <p className="text-gray-600 dark:text-gray-300 font-medium text-sm">No networks found</p>
                    <p className="text-gray-500 dark:text-gray-300 text-xs mt-1">Click "Scan" to search for WiFi networks</p>
                  </div>
                </div>
              </div>
            )}
          </div>

          {/* Connection Form */}
          {selectedSSID && (
            <div className="bg-white dark:bg-gray-900/70 backdrop-blur-sm rounded-lg p-6 border border-gray-200 dark:border-gray-700 shadow-sm">
              <div className="mb-4">
                <h3 className="text-lg font-bold bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] bg-clip-text text-transparent flex items-center gap-2">
                  <div className="p-2 bg-[#8c7ae6]/10 rounded-full">
                    <Wifi className="w-4 h-4 text-[#8c7ae6]" />
                  </div>
                  Connect to Network
                </h3>
                <div className="h-1 w-28 bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] rounded-full mt-1"></div>
              </div>

              <form onSubmit={handleSubmit} className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">Selected Network</label>
                  <div className="p-3 bg-white dark:bg-gray-900/70 border border-gray-300 rounded-lg shadow-sm">
                    <div className="flex items-center gap-3">
                      <div className="p-2 bg-[#8c7ae6]/10 rounded-full">
                        <Wifi className="w-4 h-4 text-[#8c7ae6]" />
                      </div>
                      <span className="font-bold text-gray-800">{selectedSSID}</span>
                    </div>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Password (if required)</label>
                  <div className="relative">
                    <input
                      type={showPassword ? 'text' : 'password'}
                      value={password}
                      onChange={(e) => {
                        setPassword(e.target.value);
                        if (connectError) setConnectError('');
                      }}
                      placeholder="Enter WiFi password"
                      className={`w-full px-4 py-3 border border-gray-300 rounded-lg bg-white dark:bg-gray-900/70 backdrop-blur-sm focus:ring-2 focus:ring-[#8c7ae6]/50 focus:border-[#8c7ae6] transition-all duration-200 pr-12 text-sm ${
                        connectError ? 'border-red-300 focus:border-red-500 focus:ring-red-500' : ''
                      }`}
                    />
                    <button
                      type="button"
                      onClick={() => setShowPassword(!showPassword)}
                      className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 dark:text-gray-300 transition-colors p-1"
                    >
                      {showPassword ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
                    </button>
                  </div>
                  {connectError && (
                    <div className="mt-3 flex items-center gap-2 text-red-600 text-sm bg-red-50 p-3 rounded-lg border border-red-200">
                      <XCircle className="w-4 h-4 flex-shrink-0" />
                      <span className="font-medium">{connectError}</span>
                    </div>
                  )}
                </div>


                <div className="flex gap-4 pt-2">
                  <button
                    type="button"
                    onClick={handleClose}
                    className="flex-1 px-6 py-3 border border-gray-300 dark:border-gray-700 text-gray-700 dark:text-gray-300 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-800 transition-all duration-200 font-semibold text-sm shadow-sm hover:shadow-md"
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    disabled={isConnecting}
                    className="flex-1 px-6 py-3 bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] text-white rounded-lg hover:from-[#007a93] hover:to-[#147015] transition-all duration-200 font-semibold text-sm shadow-md hover:shadow-lg disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    {isConnecting ? (
                      <div className="flex items-center justify-center gap-2">
                        <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                        Connecting...
                      </div>
                    ) : (
                      'Connect'
                    )}
                  </button>
                </div>
              </form>
            </div>
          )}
        </div>
      </div>
    </div>,
    document.body
  );
};

export default WifiSettingsModal;
