import React, { useState, useEffect } from 'react';
import {
  Settings as SettingsIcon,
  Save,
  Lock,
  Eye,
  EyeOff,
  Building
} from 'lucide-react';
import Toast from '../components/Toast';
import Spinner from '../components/Spinner';
import DateTimePicker from '../components/DateTimePicker';
import { useAppContext } from '../contexts/AppContext';
import { useAuth } from '../contexts/AuthContext';
import ApiService from '../services/apiService';

const Settings = () => {
  const { appConfig, updateConfig, isLoading } = useAppContext();
  const { changePassword } = useAuth();
  
  const [config, setConfig] = useState({
    siteName: '',
    deviceTime: '',
    timezone: 'UTC'
  });
  
  const [passwords, setPasswords] = useState({
    newPassword: '',
    confirmPassword: ''
  });
  
  const [showPasswords, setShowPasswords] = useState({
    new: false,
    confirm: false
  });
  
  const [saving, setSaving] = useState(false);
  const [toast, setToast] = useState(null);

  // Timezone options
  const timezones = [
    { value: 'UTC', label: 'UTC (Coordinated Universal Time)' },
    { value: 'Asia/Karachi', label: 'Karachi (PKT, +0500)' },
    { value: 'Asia/Shanghai', label: 'Shanghai (CST, +0800)' },
    { value: 'Asia/Tokyo', label: 'Tokyo (JST, +0900)' },
    { value: 'Asia/Kolkata', label: 'Kolkata (IST, +0530)' },
    { value: 'Asia/Dubai', label: 'Dubai (GST, +0400)' },
    { value: 'Asia/Singapore', label: 'Singapore (SGT, +0800)' },
    { value: 'Asia/Hong_Kong', label: 'Hong Kong (HKT, +0800)' },
    { value: 'Asia/Seoul', label: 'Seoul (KST, +0900)' },
    { value: 'Asia/Bangkok', label: 'Bangkok (ICT, +0700)' },
    { value: 'America/New_York', label: 'Eastern Time (ET, -0500)' },
    { value: 'America/Chicago', label: 'Central Time (CT, -0600)' },
    { value: 'America/Denver', label: 'Mountain Time (MT, -0700)' },
    { value: 'America/Los_Angeles', label: 'Pacific Time (PT, -0800)' },
    { value: 'Europe/London', label: 'London (GMT, +0000)' },
    { value: 'Europe/Paris', label: 'Paris (CET, +0100)' },
    { value: 'Australia/Sydney', label: 'Sydney (AEDT, +1100)' }
  ];

  // Load current hardware time and timezone
  useEffect(() => {
    const loadHardwareInfo = async () => {
      try {
        // Get both hardware time and timezone
        const [timeResponse, timezoneResponse] = await Promise.all([
          ApiService.getTime(),
          ApiService.getTimezone()
        ]);

        let deviceTime = '';
        let timezone = 'UTC';

        // Handle time
        if (timeResponse.time) {
          // Hardware time is in format "2025-09-16T19:50:00"
          const timeString = timeResponse.time;
          const date = new Date(timeString);
          if (!isNaN(date.getTime())) {
            deviceTime = timeString.slice(0, 16); // Convert to datetime-local format
          }
        }

        // Handle timezone
        if (timezoneResponse.timezone) {
          timezone = timezoneResponse.timezone;
        }

        setConfig({
          siteName: appConfig.siteName || '',
          deviceTime: deviceTime || new Date().toISOString().slice(0, 16),
          timezone: timezone
        });

      } catch (error) {
        console.log('Using fallback values');
        setConfig({
          siteName: appConfig.siteName || '',
          deviceTime: new Date().toISOString().slice(0, 16),
          timezone: 'UTC'
        });
      }
    };

    loadHardwareInfo();
  }, [appConfig]);

  const showToast = (message, type = 'info') => {
    setToast({ message, type });
  };

  const hideToast = () => {
    setToast(null);
  };

  const handleConfigChange = (field, value) => {
    setConfig(prev => ({ ...prev, [field]: value }));
  };

  const handlePasswordChange = (field, value) => {
    setPasswords(prev => ({ ...prev, [field]: value }));
  };

  const handleSaveConfig = async () => {
    try {
      setSaving(true);
      
      // Update timezone first (this affects how time is interpreted)
      if (config.timezone) {
        await ApiService.setTimezone(config.timezone);
        showToast('Timezone updated to ' + config.timezone, 'success');
      }
      
      // Update system time (in the new timezone)
      if (config.deviceTime) {
        // Convert datetime-local format to server format: "2025-09-16T19:50" -> "2025-09-16 19:50:00"
        const timeString = config.deviceTime.replace('T', ' ') + ':00';
        await ApiService.setTime(timeString);
        showToast('Time updated to ' + timeString, 'success');
      }
      
      // Save site configuration
      await updateConfig(config);
      
      showToast(' All settings saved successfully!', 'success');
    } catch (error) {
      console.error('Error saving config:', error);
      showToast('Failed to save configuration: ' + error.message, 'error');
    } finally {
      setSaving(false);
    }
  };



  const handleChangePassword = async () => {
    if (passwords.newPassword !== passwords.confirmPassword) {
      showToast('New passwords do not match', 'error');
      return;
    }

    if (passwords.newPassword.length < 6) {
      showToast('Password must be at least 6 characters long', 'error');
      return;
    }

    try {
      setSaving(true);
      const result = await changePassword(passwords.newPassword);
      
      if (result.success) {
        showToast('Password changed successfully!', 'success');
        setPasswords({ newPassword: '', confirmPassword: '' });
      } else {
        showToast(result.message || 'Password change failed', 'error');
      }
    } catch (error) {
      console.error('Password change error:', error);
      showToast('Password change failed. Please try again.', 'error');
    } finally {
      setSaving(false);
    }
  };

  const togglePasswordVisibility = (field) => {
    setShowPasswords(prev => ({ ...prev, [field]: !prev[field] }));
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <Spinner size="lg" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-[#f2f4f7] dark:bg-gray-950 p-6">
      <div className="max-w-4xl mx-auto space-y-6">
        {/* Header - Matching gfe-sphere style */}
        <div className="mb-6">
          <div className="bg-white dark:bg-gray-900/70 rounded-xl border border-[#24292e1f] dark:border-gray-800/50 px-4 py-4">
            <div className="flex items-center justify-between">
              <div>
                <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-1">
                  Settings
                </h1>
                <p className="text-sm text-gray-600 dark:text-gray-300">
                  Configure your application preferences
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* Site Configuration */}
        <div className="bg-white dark:bg-gray-900/70 rounded-xl border border-[#24292e1f] dark:border-gray-800/50 shadow-sm p-6">
          <div className="flex items-center space-x-3 mb-6">
            <Building className="w-5 h-5 text-[#8c7ae6]" />
            <h2 className="text-xl font-semibold text-gray-900 dark:text-white">Site Configuration</h2>
          </div>

          <div className="space-y-6">
            {/* Site Name */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                Site Name
              </label>
              <input
                type="text"
                value={config.siteName}
                onChange={(e) => handleConfigChange('siteName', e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] transition-all duration-200 text-gray-900 dark:text-white placeholder-gray-500"
                placeholder="Enter your site name"
              />
            </div>

            {/* Device Time */}
            <div>
              <DateTimePicker
                label="Device Time"
                  value={config.deviceTime}
                onChange={(value) => handleConfigChange('deviceTime', value)}
                placeholder="Select device date & time"
                showSeconds={false}
                />
              <p className="text-sm text-gray-500 dark:text-gray-300 mt-2">
                Set the device time. The form will show the current hardware time when you open this page.
              </p>
            </div>

            {/* Timezone */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                Timezone
              </label>
              <select
                value={config.timezone}
                onChange={(e) => handleConfigChange('timezone', e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] transition-all duration-200 text-gray-900 dark:text-white bg-white dark:bg-gray-900/70"
              >
                {timezones.map(tz => (
                  <option key={tz.value} value={tz.value}>
                    {tz.label}
                  </option>
                ))}
              </select>
            </div>
          </div>

          <div className="mt-8 flex justify-end ">
            <button
              onClick={handleSaveConfig}
              disabled={saving}
              className="flex items-center space-x-2 px-6 py-2.5 bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] hover:opacity-90 text-white font-medium rounded-lg transition-opacity duration-200 shadow-sm hover:shadow-md disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {saving ? (
                <Spinner size="sm" centered={false} className="w-4 h-4" />
              ) : (
                <Save className="w-4 h-4" />
              )}
              <span>{saving ? 'Saving...' : 'Save All Settings'}</span>
            </button>
          </div>
        </div>



        {/* Password Settings */}
        <div className="bg-white dark:bg-gray-900/70 rounded-xl border border-[#24292e1f] dark:border-gray-800/50 shadow-sm p-6">
          <div className="flex items-center space-x-3 mb-6">
            <Lock className="w-5 h-5 text-[#8c7ae6]" />
            <h2 className="text-xl font-semibold text-gray-900 dark:text-white">Password Settings</h2>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 ">
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                New Password
              </label>
              <div className="relative">
                <input
                  type={showPasswords.new ? 'text' : 'password'}
                  value={passwords.newPassword}
                  onChange={(e) => handlePasswordChange('newPassword', e.target.value)}
                  className="w-full px-4 py-3 pr-12 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 transition-all duration-200 text-gray-900 dark:text-white placeholder-gray-500"
                  placeholder="Enter new password"
                />
                <button
                  type="button"
                  onClick={() => togglePasswordVisibility('new')}
                  className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 dark:text-gray-300 transition-colors"
                >
                  {showPasswords.new ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
                </button>
              </div>
              <p className="text-sm text-gray-500 dark:text-gray-300 mt-2">
                Minimum 6 characters required
              </p>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                Confirm New Password
              </label>
              <div className="relative">
                <input
                  type={showPasswords.confirm ? 'text' : 'password'}
                  value={passwords.confirmPassword}
                  onChange={(e) => handlePasswordChange('confirmPassword', e.target.value)}
                  className="w-full px-4 py-3 pr-12 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500 transition-all duration-200 text-gray-900 dark:text-white placeholder-gray-500"
                  placeholder="Confirm new password"
                />
                <button
                  type="button"
                  onClick={() => togglePasswordVisibility('confirm')}
                  className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 dark:text-gray-300 transition-colors"
                >
                  {showPasswords.confirm ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
                </button>
              </div>
            </div>
          </div>

          <div className="mt-8 flex justify-end ">
            <button
              onClick={handleChangePassword}
              disabled={!passwords.newPassword || !passwords.confirmPassword || saving}
              className="flex items-center space-x-2 px-6 py-2.5 bg-red-500 hover:bg-red-600 text-white font-medium rounded-lg transition-colors duration-200 shadow-sm hover:shadow-md disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {saving ? (
                <Spinner size="sm" centered={false} className="w-4 h-4" />
              ) : (
                <Lock className="w-4 h-4" />
              )}
              <span>{saving ? 'Changing Password...' : 'Change Password'}</span>
            </button>
          </div>
        </div>
      </div>

      {/* Toast Notification */}
      {toast && (
        <Toast
          message={toast.message}
          type={toast.type}
          onClose={hideToast}
        />
      )}
    </div>
  );
};

export default Settings; 