import React, { useState, useEffect, useRef } from 'react';
import { Search, Filter, Server, Wifi, Cable, Router, Globe2, CheckCircle, XCircle, AlertCircle, ArrowUpDown, ArrowUp, ArrowDown, RefreshCw } from 'lucide-react';
import { ToastContainer, toast } from 'react-toastify';
import DeviceDetailsSlider from '../components/DeviceDetailsSlider';
import ApiService from '../services/apiService';
import { useSorting } from '../hooks/useSorting';
import SortableTableHeader from '../components/SortableTableHeader';
import { useApiCall } from '../hooks/useApiCall';
import Spinner from '../components/Spinner';
import { 
  getInterfaceDisplayName, 
  getInterfaceBadge, 
  getTypeBadge, 
  getStatusBadge,
  getInterfaceIcon
} from '../utils/deviceUtils';
import 'react-toastify/dist/ReactToastify.css';

const Devices = () => {
  const [devices, setDevices] = useState([]);
  const [filteredDevices, setFilteredDevices] = useState([]);
  const [references, setReferences] = useState([]);
  const [deviceStatuses, setDeviceStatuses] = useState({});
  const [loading, setLoading] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedInterface, setSelectedInterface] = useState('all');
  const [selectedType, setSelectedType] = useState('all');
  const [selectedStatus, setSelectedStatus] = useState('all');
  const [selectedDevice, setSelectedDevice] = useState(null);
  const [isSliderOpen, setIsSliderOpen] = useState(false);

  // Sorting state using reusable hook
  const {
    sortConfig,
    handleSort: handleSortInternal,
    sortData,
    resetSorting
  } = useSorting('device_name', 'asc'); // Default: sort by device name, ascending

  // Wrapper function to convert SortableTableHeader's sort format to useSorting's format
  const handleSort = (sortKey) => {
    handleSortInternal(sortKey);
  };



  // Fetch device statuses from database
  const fetchDeviceStatuses = async () => {
    try {
      const statusData = await ApiService.getDeviceStatuses();
      if (statusData.success && statusData.device_statuses) {
        setDeviceStatuses(statusData.device_statuses);
      }
    } catch (error) {
      console.error('Error fetching device statuses:', error);
    }
  };

  // Use the API call hook to prevent duplicates
  const { wrappedApiCall: wrappedFetchDevices, cleanup: cleanupFetchDevices } = useApiCall(
    async () => {
      const data = await ApiService.getDevices();
      
      // Flatten devices from all interfaces
      const allDevices = [];
      Object.entries(data.devices || {}).forEach(([interfaceName, interfaceDevices]) => {
        interfaceDevices.forEach(device => {
          allDevices.push({
            ...device,
            interface: interfaceName,
            // Status will be updated from deviceStatuses
            status: '--'
          });
        });
      });
      
      setDevices(allDevices);
      setReferences(data.references || []);
      
      // Fetch device statuses after getting devices
      await fetchDeviceStatuses();
      
      return data;
    },
    loading,
    setLoading
  );

  useEffect(() => {
    wrappedFetchDevices();
    
    // Set up auto-refresh for device statuses every 30 seconds
    const statusInterval = setInterval(() => {
      fetchDeviceStatuses();
    }, 30000);
    
    return () => {
      cleanupFetchDevices();
      clearInterval(statusInterval);
    };
  }, []);

  useEffect(() => {
    filterDevices();
  }, [devices, searchQuery, selectedInterface, selectedType, selectedStatus, sortConfig]);

  // Update device statuses when deviceStatuses changes
  useEffect(() => {
    if (Object.keys(deviceStatuses).length > 0) {
      setDevices(prevDevices => 
        prevDevices.map(device => ({
          ...device,
          status: deviceStatuses[device.device_name]?.status || 'unknown'
        }))
      );
    }
  }, [deviceStatuses]);

  const filterDevices = () => {
    let filtered = devices;

    // Search filter
    if (searchQuery.trim()) {
      filtered = filtered.filter(device =>
        device.device_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        device.reference.toLowerCase().includes(searchQuery.toLowerCase()) ||
        device.device_type?.toLowerCase().includes(searchQuery.toLowerCase()) ||
        device.interface.toLowerCase().includes(searchQuery.toLowerCase())
      );
    }

    // Interface filter
    if (selectedInterface !== 'all') {
      filtered = filtered.filter(device => device.interface === selectedInterface);
    }

    // Type filter
    if (selectedType !== 'all') {
      filtered = filtered.filter(device => device.device_type === selectedType);
    }

    // Status filter
    if (selectedStatus !== 'all') {
      filtered = filtered.filter(device => device.status === selectedStatus);
    }

    // Apply sorting
    filtered = sortData(filtered);

    setFilteredDevices(filtered);
  };

  const getStatusIcon = (status) => {
    if (status === '--') {
      return <Server className="w-5 h-5 text-gray-400 dark:text-gray-500" />;
    }
    switch (status) {
      case 'online':
        return <CheckCircle className="w-5 h-5 text-green-500" />;
      case 'offline':
        return <XCircle className="w-5 h-5 text-gray-400 dark:text-gray-500" />;
      case 'error':
        return <AlertCircle className="w-5 h-5 text-red-500" />;
      default:
        return <Server className="w-5 h-5 text-gray-400 dark:text-gray-500" />;
    }
  };





  const openDeviceDetails = (device) => {
    setSelectedDevice(device);
    setIsSliderOpen(true);
  };

  const closeDeviceDetails = () => {
    setIsSliderOpen(false);
    // Small delay to allow animation to complete before clearing device
    setTimeout(() => setSelectedDevice(null), 500);
  };

  const uniqueTypes = [...new Set(devices.map(d => d.device_type).filter(Boolean))];
  const uniqueInterfaces = [...new Set(devices.map(d => d.interface))];

  return (
    <div className="min-h-screen bg-[#f2f4f7] dark:bg-gray-950 p-6">

      {/* Header - Matching gfe-sphere style */}
      <div className="mb-6 relative z-10">
        <div className="bg-white dark:bg-gray-900/70 rounded-xl border border-[#24292e1f] dark:border-gray-800/50 px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-1">
                Device Management
              </h1>
              <p className="text-sm text-gray-600 dark:text-gray-300">
                Monitor and manage all connected devices
              </p>
            </div>
          
            <div className="flex items-center gap-2">
              <span className="text-sm text-gray-600 dark:text-gray-300">
                {devices.length} {devices.length === 1 ? 'Device' : 'Devices'}
              </span>
            </div>
          </div>
        </div>
      </div>

      <ToastContainer 
        autoClose={3000} 
        newestOnTop 
        closeOnClick 
        pauseOnHover={false} 
        theme="colored"
        toastClassName=""
        position="top-right"
      />

      {/* Search and Filters */}
      <div className="mb-8 relative z-10">
        <div className="bg-white dark:bg-gray-900/70 rounded-xl border border-[#24292e1f] dark:border-gray-800/50 shadow-sm p-6">
          <div className="space-y-6">
            {/* Search Bar */}
            <div className="relative">
              <div className="absolute inset-y-0 left-0 pl-4 flex items-center pointer-events-none">
                <Search className="h-5 w-5 text-[#8c7ae6]" />
              </div>
              <input
                type="text"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="block w-full pl-12 pr-4 py-3 border border-gray-300 dark:border-gray-700 rounded-lg text-sm placeholder-gray-500 dark:placeholder-gray-400 focus:ring-2 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] bg-white dark:bg-gray-900 text-gray-900 dark:text-white transition-all duration-200"
                placeholder="Search devices by name, reference, type, or interface..."
              />
            </div>

            {/* Filters Row */}
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              {/* Interface Filter */}
              <div>
                <label className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-2">Interface</label>
                <select
                  value={selectedInterface}
                  onChange={(e) => setSelectedInterface(e.target.value)}
                  className="w-full border border-gray-300 rounded-lg px-4 py-2.5 text-sm focus:ring-2 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] bg-white dark:bg-gray-900/70"
                >
                  <option value="all">All Interfaces</option>
                  {uniqueInterfaces.map(iface => (
                    <option key={iface} value={iface}>{getInterfaceDisplayName(iface)}</option>
                  ))}
                </select>
              </div>

              {/* Type Filter */}
              <div>
                <label className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-2">Device Type</label>
                <select
                  value={selectedType}
                  onChange={(e) => setSelectedType(e.target.value)}
                  className="w-full border border-gray-300 rounded-lg px-4 py-2.5 text-sm focus:ring-2 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] bg-white dark:bg-gray-900/70"
                >
                  <option value="all">All Types</option>
                  {uniqueTypes.map(type => (
                    <option key={type} value={type}>
                      {type.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase())}
                    </option>
                  ))}
                </select>
              </div>

              {/* Status Filter */}
              <div>
                <label className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-2">Status</label>
                <select
                  value={selectedStatus}
                  onChange={(e) => setSelectedStatus(e.target.value)}
                  className="w-full border border-gray-300 rounded-lg px-4 py-2.5 text-sm focus:ring-2 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] bg-white dark:bg-gray-900/70"
                >
                  <option value="all">All Status</option>
                  <option value="connected">Connected</option>
                  <option value="disconnected">Disconnected</option>
                </select>
              </div>

              {/* Results Counter */}
              <div className="flex items-end">
                <div className="w-full px-4 py-2.5 bg-gradient-to-r from-[#8c7ae6]/10 to-[#a78bfa]/10 rounded-lg border border-[#8c7ae6]/30">
                  <div className="flex items-center gap-2">
                    <Filter size={16} className="text-[#8c7ae6]" />
                    <span className="text-sm font-medium text-[#8c7ae6]">
                      {filteredDevices.length} of {devices.length} devices
                    </span>
                  </div>
                </div>
              </div>
            </div>
            
          </div>
        </div>
      </div>

      {/* Devices Table */}
      <div className="bg-white dark:bg-gray-900/70 rounded-xl border border-[#24292e1f] dark:border-gray-800/50 overflow-hidden" style={{ opacity: 1 }}>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700" style={{ WebkitFontSmoothing: 'antialiased', MozOsxFontSmoothing: 'grayscale', textRendering: 'optimizeLegibility' }}>
                <thead className="bg-[#f2f4f7] dark:bg-gray-800 sticky top-0 z-10">
                  <tr>
                    <SortableTableHeader
                      label="Device"
                      sortKey="device_name"
                      currentSortBy={sortConfig.key}
                      currentSortOrder={sortConfig.direction === 'asc' ? 'ASC' : 'DESC'}
                      onSort={handleSort}
                    />
                    <SortableTableHeader
                      label="Reference"
                      sortKey="reference"
                      currentSortBy={sortConfig.key}
                      currentSortOrder={sortConfig.direction === 'asc' ? 'ASC' : 'DESC'}
                      onSort={handleSort}
                    />
                    <SortableTableHeader
                      label="Type"
                      sortKey="device_type"
                      currentSortBy={sortConfig.key}
                      currentSortOrder={sortConfig.direction === 'asc' ? 'ASC' : 'DESC'}
                      onSort={handleSort}
                    />
                    <SortableTableHeader
                      label="Interface"
                      sortKey="interface"
                      currentSortBy={sortConfig.key}
                      currentSortOrder={sortConfig.direction === 'asc' ? 'ASC' : 'DESC'}
                      onSort={handleSort}
                    />
                    <SortableTableHeader
                      label="Status"
                      sortKey="status"
                      currentSortBy={sortConfig.key}
                      currentSortOrder={sortConfig.direction === 'asc' ? 'ASC' : 'DESC'}
                      onSort={handleSort}
                    />
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                      Details
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white dark:bg-gray-900/70 divide-y divide-gray-200 dark:divide-gray-700" style={{ WebkitFontSmoothing: 'antialiased', textRendering: 'optimizeLegibility' }}>
                  {loading ? (
                    <tr>
                      <td colSpan={6} className="px-6 py-12">
                        <Spinner size="md" />
                      </td>
                    </tr>
                  ) : filteredDevices.length === 0 ? (
                    <tr>
                      <td colSpan={6} className="px-6 py-12 text-center">
                        <div className="flex flex-col items-center gap-3">
                          <Server className="w-12 h-12 text-gray-400 dark:text-gray-500" />
                          <div>
                            <p className="text-sm font-medium text-gray-900 dark:text-white">No devices found</p>
                            <p className="text-sm text-gray-600 dark:text-gray-300 mt-1">
                              {searchQuery || selectedInterface !== 'all' || selectedType !== 'all' || selectedStatus !== 'all'
                                ? 'Try adjusting your search or filters'
                                : 'No devices have been configured yet'
                              }
                            </p>
                          </div>
                        </div>
                      </td>
                    </tr>
                  ) : (
                    filteredDevices.map((device, index) => (
                      <tr 
                        key={`${device.interface}-${device.device_name}-${index}`} 
                        onClick={() => openDeviceDetails(device)}
                        className="hover:bg-blue-50 dark:hover:bg-gray-700/50 cursor-pointer transition-colors"
                      >
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div className="text-sm font-medium text-gray-900 dark:text-white">
                            {device.device_name}
                          </div>
                          {device.device_id && (
                            <div className="text-xs text-gray-500 dark:text-gray-300 mt-0.5">
                              ID: {device.device_id}
                            </div>
                          )}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div className="text-sm text-gray-600 dark:text-gray-300">
                            {device.reference || ''}
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          {getTypeBadge(device)}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div className="flex items-center gap-2">
                            {getInterfaceIcon(device.interface, { Router, Wifi, Cable, Server })}
                            <span className="text-sm text-gray-600 dark:text-gray-300">
                              {getInterfaceDisplayName(device.interface)}
                            </span>
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span className={getStatusBadge(device.status)}>
                            <span className="capitalize">{device.status || ''}</span>
                          </span>
                        </td>
                        <td className="px-6 py-4">
                          <div className="text-sm text-gray-600 dark:text-gray-300">
                            <div>Protocol: {device.protocol || ''}</div>
                            {device.device_ip && (
                              <div className="flex items-center gap-1.5 text-xs mt-1">
                                <Globe2 className="w-3 h-3" />
                                <span>{device.device_ip}:{device.tcp_port || ''}</span>
                              </div>
                            )}
                          </div>
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
          </div>

      {/* Device Details Slider */}
      <DeviceDetailsSlider 
        device={selectedDevice}
        isOpen={isSliderOpen}
        onClose={closeDeviceDetails}
      />
    </div>
  );
};

export default Devices;
