import React, { useState, useEffect } from 'react';
import { useAppSelector } from '../../hooks/redux';
import { useAppContext } from '../../contexts/AppContext';
import { Wifi, WifiOff, Bell } from 'lucide-react';
import ApiService from '../../services/apiService';
import ProfileDropdown from '../ProfileDropdown';

const Header = () => {
  const { unreadCount } = useAppSelector((state) => state.alerts);
  const { appConfig } = useAppContext();
  
  const [currentTime, setCurrentTime] = useState(null);
  const [isOnline, setIsOnline] = useState(true);
  const [timeLoading, setTimeLoading] = useState(true);

  // Get hardware time once and count locally, with periodic sync
  useEffect(() => {
    const initializeHardwareTime = async () => {
      try {
        const response = await ApiService.getTime();
        if (response.time) {
          // Hardware time is in format "2025-09-16T19:50:00" (local time)
          const timeString = response.time;
          const hardwareTime = new Date(timeString);
          if (!isNaN(hardwareTime.getTime())) {
            setCurrentTime(hardwareTime);
            setTimeLoading(false);
            console.log('Hardware time loaded:', hardwareTime);
          } else {
            throw new Error('Invalid time format');
          }
        }
      } catch (error) {
        console.log('Using client time as fallback');
        setCurrentTime(new Date());
        setTimeLoading(false);
      }
    };

    // Get hardware time once on mount
    initializeHardwareTime();
    
    // Sync with hardware every 30 seconds to prevent drift
    const syncInterval = setInterval(async () => {
      try {
        const response = await ApiService.getTime();
        if (response.time) {
          const timeString = response.time;
          const hardwareTime = new Date(timeString);
          if (!isNaN(hardwareTime.getTime())) {
            setCurrentTime(hardwareTime);
            console.log('Hardware time synced:', hardwareTime);
          }
        }
      } catch (error) {
        console.warn('Failed to sync with hardware time:', error);
        // Continue with local time if sync fails
      }
    }, 30000); // Sync every 30 seconds
    
    // Then update every second using local timer
    const timer = setInterval(() => {
      setCurrentTime(prevTime => {
        if (prevTime) {
          return new Date(prevTime.getTime() + 1000);
        }
        return prevTime;
      });
    }, 1000);
    
    return () => {
      clearInterval(timer);
      clearInterval(syncInterval);
    };
  }, []);

  // Check connectivity periodically using Google DNS ping
  useEffect(() => {
    const checkConnectivity = async () => {
      try {
        const response = await ApiService.ping();
        // Use the enhanced ping response that checks 8.8.8.8
        setIsOnline(response.connectivity === true);
      } catch (error) {
        setIsOnline(false);
      }
    };

    checkConnectivity();
    const interval = setInterval(checkConnectivity, 30000); // Check every 30 seconds
    return () => clearInterval(interval);
  }, []);

  return (
    <header className="bg-white dark:bg-gray-800 border-b border-gray-200 dark:border-gray-700 relative">
      <div className="flex items-center justify-between px-6 py-4 h-16 relative z-10">
        {/* Site Name */}
        <div className="flex items-center gap-3">
          <h1 className="text-xl font-bold text-gray-900 dark:text-white transition-all duration-300">
            {appConfig.siteName}
          </h1>
        </div>

        {/* Center: Empty space for cleaner layout */}
        <div></div>

        {/* Right: Status Icons */}
        <div className="flex items-center space-x-3">
          {/* Time Display */}
          <div className="px-3 py-2 bg-gray-50 dark:bg-gray-700 rounded-lg border border-gray-200 dark:border-gray-600 shadow-sm">
            {timeLoading ? (
              <span className="text-lg font-bold text-gray-400 dark:text-gray-500">
                --:--:--
              </span>
            ) : currentTime ? (
              <span className="text-lg font-bold text-gray-900 dark:text-white">
                {currentTime.toLocaleTimeString('en-US', { hour12: true, hour: '2-digit', minute: '2-digit', second: '2-digit' })}
              </span>
            ) : (
              <span className="text-lg font-bold text-gray-400 dark:text-gray-500">
                --:--:--
              </span>
            )}
          </div>
          
          {/* WiFi/Connectivity Status */}
          <div className="flex items-center px-3 py-2 bg-gray-50 dark:bg-gray-700 rounded-lg border border-gray-200 dark:border-gray-600 shadow-sm">
            <div className="relative">
              {isOnline ? (
                <Wifi size={20} className="text-[#8c7ae6]" />
              ) : (
                <WifiOff size={20} className="text-red-500" />
              )}
              {isOnline && (
                <div className="absolute -top-1 -right-1 w-3 h-3 bg-[#8c7ae6] rounded-full animate-ping"></div>
              )}
            </div>
          </div>

          {/* Alerts */}
          <div className="relative">
            <button className="p-2 bg-gray-50 dark:bg-gray-700 rounded-lg border border-gray-200 dark:border-gray-600 shadow-sm hover:bg-gray-100 dark:hover:bg-gray-600 transition-all duration-200">
              <Bell size={20} className="text-[#8c7ae6]" />
              {unreadCount > 0 && (
                <div className="absolute -top-1 -right-1 w-5 h-5 bg-gradient-to-r from-red-500 to-red-600 rounded-full flex items-center justify-center text-xs text-white font-bold animate-pulse">
                  {unreadCount}
                </div>
              )}
            </button>
          </div>

          {/* Profile Dropdown */}
          <ProfileDropdown />
        </div>
      </div>
    </header>
  );
};

export default Header; 