// API Service utility for centralized API calls
const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5001';

class ApiService {
  static async request(endpoint, options = {}) {
    const url = `${API_BASE_URL}${endpoint}`;
    
    // Get auth token from localStorage
    const token = localStorage.getItem('authToken');
    
    const defaultOptions = {
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
    };

    // Add authorization header if token exists
    if (token) {
      defaultOptions.headers['Authorization'] = `Bearer ${token}`;
    }

    const config = {
      ...defaultOptions,
      ...options,
      headers: {
        ...defaultOptions.headers,
        ...options.headers,
      },
    };

    try {
      const response = await fetch(url, config);
      
      if (!response.ok) {
        // Handle 401/403 authentication errors
        if (response.status === 401 || response.status === 403) {
          // Clear invalid token
          localStorage.removeItem('authToken');
          // Don't redirect for auth endpoints to avoid loops
          if (!endpoint.includes('/auth/')) {
            const base = typeof document !== 'undefined' && document.querySelector('base')?.href
              ? (() => { try { const p = new URL(document.querySelector('base').href, window.location.origin).pathname; return p.replace(/\/$/, '') || ''; } catch (e) { return ''; } })()
              : '';
            window.location.href = base + '/';
          }
          // For auth endpoints, let the calling code handle the error
          if (endpoint.includes('/auth/')) {
            const errorData = await response.json().catch(() => ({}));
            throw new Error(errorData.message || errorData.error || 'Authentication failed');
          }
          throw new Error('Authentication required');
        }
        
        // Try to parse error response for structured error messages
        try {
          const errorData = await response.json();
          
          // Handle different error response structures
          if (errorData.error) {
            // Create error with detailed information
            const error = new Error(errorData.error);
            error.code = errorData.errorCode || errorData.code;
            error.status = response.status;
            error.details = errorData.details;
            error.type = errorData.type;
            error.protocol = errorData.protocol;
            error.target = errorData.target;
            error.deviceName = errorData.deviceName;
            error.responseTime = errorData.responseTime;
            error.timestamp = errorData.timestamp;
            throw error;
          } else if (errorData.code && errorData.message) {
            // Create error with structured data
            const error = new Error(errorData.message);
            error.code = errorData.code;
            error.status = response.status;
            throw error;
          } else if (errorData.message) {
            throw new Error(errorData.message);
          } else {
            throw new Error(`HTTP error! status: ${response.status}`);
          }
        } catch (parseError) {
          // If JSON parsing fails, fall back to generic error
          if (parseError.code || parseError.details) {
            // This is our structured error, re-throw it
            throw parseError;
          }
          throw new Error(`HTTP error! status: ${response.status}`);
        }
      }
      
      return await response.json();
    } catch (error) {
      console.error(`API request failed for ${endpoint}:`, error);
      throw error;
    }
  }

  // Config API
  static async getConfig() {
    return this.request('/config');
  }

  static async updateConfig(configData) {
    return this.request('/config', {
      method: 'PUT',
      body: JSON.stringify(configData),
    });
  }

  // Time API
  static async getTime() {
    return this.request('/time');
  }

  static async getTimezone() {
    return this.request('/timezone');
  }

  static async setTime(time) {
    return this.request('/time', {
      method: 'POST',
      body: JSON.stringify({ time }),
    });
  }

  static async setTimezone(timezone) {
    return this.request('/timezone', {
      method: 'POST',
      body: JSON.stringify({ timezone }),
    });
  }

  // Health/Ping API
  static async ping() {
    return this.request('/ping');
  }

  static async getHealth() {
    return this.request('/health');
  }


  // System Information API
  static async getSystemInfo() {
    return this.request('/system-info');
  }



  // Devices API
  static async getDevices() {
    return this.request('/devices');
  }

  static async getDeviceStatuses() {
    return this.request('/devices/statuses');
  }

  // WiFi API
  static async getNearbyWifiNetworks() {
    return this.request('/wifi/nearby');
  }

  static async connectToWifi(ssid, password = '') {
    return this.request('/wifi/connect', {
      method: 'POST',
      body: JSON.stringify({ ssid, password })
    });
  }

  static async getWifiStatus() {
    return this.request('/wifi/status');
  }

  static async disconnectFromWifi() {
    return this.request('/wifi/disconnect', {
      method: 'POST'
    });
  }

  static async saveWifiConnection(ssid, password, remember) {
    return this.request('/wifi/save', {
      method: 'POST',
      body: JSON.stringify({ ssid, password, remember })
    });
  }

  static async getSavedWifiConnections() {
    return this.request('/wifi/saved');
  }

  static async autoConnectToSavedWifi() {
    return this.request('/wifi/auto-connect', {
      method: 'POST'
    });
  }

  static async getDevicesForInterface(interfaceName) {
    return this.request(`/devices/${interfaceName}`);
  }

  static async createDevice(deviceData) {
    return this.request('/devices', {
      method: 'POST',
      body: JSON.stringify(deviceData),
    });
  }

  static async updateDevice(originalName, deviceData) {
    return this.request(`/devices/${originalName}`, {
      method: 'PUT',
      body: JSON.stringify(deviceData),
    });
  }

  static async deleteDevice(deviceName) {
    return this.request(`/devices/${deviceName}`, {
      method: 'DELETE',
    });
  }

  // Parameters API
  static async setParameterValue(parameterData) {
    return this.request('/parameters/set-value', {
      method: 'POST',
      body: JSON.stringify(parameterData),
    });
  }

  // Network API
  static async getNetworkInterfaces(only = null) {
    const endpoint = only ? `/net/ifaces?only=${only.join(',')}` : '/net/ifaces';
    return this.request(endpoint);
  }

  static async updateNetworkInterface(interfaceName, config) {
    return this.request(`/net/ifaces/${interfaceName}`, {
      method: 'PUT',
      body: JSON.stringify(config),
    });
  }

  static async getConnectivityStatus(interfaces) {
    const endpoint = interfaces ? `/net/connectivity?interfaces=${interfaces.join(',')}` : '/net/connectivity';
    return this.request(endpoint);
  }

  // Ethernet Configuration API
  static async getEthernetConfig(interfaceName = 'eth1') {
    return this.request(`/net/ethernet/config?interface=${interfaceName}`);
  }

  static async setEthernetConfig(interfaceName, config) {
    return this.request('/net/ethernet/config', {
      method: 'POST',
      body: JSON.stringify({ interface: interfaceName, ...config }),
    });
  }

  static async updateEthernetConfig(interfaceName, config) {
    return this.request('/net/ethernet/config', {
      method: 'PUT',
      body: JSON.stringify({ interface: interfaceName, ...config }),
    });
  }

  // Serial API
  static async getSerialPorts() {
    return this.request('/serial/ports');
  }

  static async updateSerialPort(portId, config) {
    return this.request(`/serial/ports/${portId}`, {
      method: 'PUT',
      body: JSON.stringify(config),
    });
  }

  // Device Connectivity Testing API
  static async testDeviceConnectivity(testConfig) {
    return this.request('/connectivity/test-connectivity', {
      method: 'POST',
      body: JSON.stringify(testConfig),
    });
  }

  // Device Blueprint API
  static async getDeviceBlueprint(reference) {
    return this.request(`/devices/blueprint/${reference}`);
  }

  static async updateDeviceBlueprint(reference, blueprintData) {
    return this.request(`/devices/blueprint/${reference}`, {
      method: 'PUT',
      body: JSON.stringify(blueprintData),
    });
  }

  // Logs API - Single endpoint that returns both logs and device names
  // Supports arrays for type and device (multi-selection)
  static async getLogs(filters = {}) {
    const params = new URLSearchParams();
    
    // Handle type - can be string or array
    if (filters.type) {
      if (Array.isArray(filters.type)) {
        filters.type.forEach(t => params.append('type', t));
      } else {
        params.append('type', filters.type);
      }
    }
    
    // Handle device - can be string or array
    if (filters.device && filters.device !== 'all') {
      if (Array.isArray(filters.device)) {
        filters.device.forEach(d => params.append('device', d));
      } else {
        params.append('device', filters.device);
      }
    }
    
    if (filters.startDate) params.append('startDate', filters.startDate);
    if (filters.endDate) params.append('endDate', filters.endDate);
    if (filters.limit) params.append('limit', filters.limit);
    if (filters.page) params.append('page', filters.page);
    if (filters.sortBy) params.append('sortBy', filters.sortBy);
    if (filters.sortOrder) params.append('sortOrder', filters.sortOrder);
    
    const queryString = params.toString();
    const url = queryString ? `/logs?${queryString}` : '/logs';
    
    return this.request(url);
  }

  // Keep these for backward compatibility but they're now redundant
  static async getLogDeviceNames(type = 'communication') {
    console.warn('getLogDeviceNames is deprecated. Use getLogs() which now returns device names.');
    return this.request(`/logs/devices?type=${type}`);
  }

  static async getLogStats(type = 'communication') {
    return this.request(`/logs/stats?type=${type}`);
  }

  // Control Config API
  static async getControlConfig() {
    return this.request('/control/config');
  }

  static async saveControlConfig(configData) {
    return this.request('/control/config', {
      method: 'PUT',
      body: JSON.stringify(configData),
    });
  }

}

export default ApiService;
