import React, { useState, useEffect } from 'react';
import { createPortal } from 'react-dom';
import { X, Lock, Eye, EyeOff, Shield, HelpCircle, Copy } from 'lucide-react';
import { useAuth } from '../contexts/AuthContext';
import apiService from '../services/apiService';

const LoginModal = ({ isOpen, onClose }) => {
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [successMessage, setSuccessMessage] = useState('');
  const [showForgotPassword, setShowForgotPassword] = useState(false);
  const [hardwareInfo, setHardwareInfo] = useState(null);
  const [isLoadingHardwareInfo, setIsLoadingHardwareInfo] = useState(false);
  const [copyFeedback, setCopyFeedback] = useState('');
  const [isForgotPasswordActive, setIsForgotPasswordActive] = useState(false);
  const { login } = useAuth();

  // Reset state when modal opens/closes
  useEffect(() => {
    if (isOpen) {
      setShowForgotPassword(false);
      setHardwareInfo(null);
      setErrorMessage('');
      setSuccessMessage('');
      setCopyFeedback('');
      setIsForgotPasswordActive(false);
    }
  }, [isOpen]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    
    // Prevent login if forgot password is active
    if (isForgotPasswordActive || isLoadingHardwareInfo) {
      return;
    }
    
    if (!password.trim()) {
      setErrorMessage('Password is required');
      return;
    }

    // Reset forgot password state when login starts
    setShowForgotPassword(false);
    setHardwareInfo(null);
    setIsForgotPasswordActive(false);

    setIsSubmitting(true);
    setErrorMessage('');
    setSuccessMessage('');
    
    try {
      const result = await login(password, false); // Pass false to disable toasts
      if (result.success) {
        setSuccessMessage('Login successful!');
        setPassword('');
        // Close modal after loader finishes (3 seconds + small delay)
        setTimeout(() => {
          handleClose();
        }, 3500);
      } else {
        setErrorMessage(result.message || 'Login failed. Please try again.');
      }
    } catch (error) {
      console.error('Login modal error:', error);
      setErrorMessage(error.message || 'Login failed. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Escape') {
      handleClose();
    }
  };

  const handleClose = () => {
    setErrorMessage('');
    setSuccessMessage('');
    setPassword('');
    setShowForgotPassword(false);
    setHardwareInfo(null);
    setCopyFeedback('');
    setIsForgotPasswordActive(false);
    onClose();
  };

  const handleForgotPassword = async (e) => {
    // Prevent event bubbling and form submission
    if (e) {
      e.preventDefault();
      e.stopPropagation();
    }
    
    // Prevent if login is in progress
    if (isSubmitting) {
      return;
    }
    
    // Set forgot password as active
    setIsForgotPasswordActive(true);
    setIsLoadingHardwareInfo(true);
    setErrorMessage('');
    setSuccessMessage('');
    
    try {
      const data = await apiService.request('/system/hardware-info', {
        method: 'GET'
      });

      if (data.success) {
        setHardwareInfo(data);
        setShowForgotPassword(true);
      } else {
        setErrorMessage(data.error || 'Failed to get hardware information');
        setIsForgotPasswordActive(false);
      }
    } catch (error) {
      console.error('Error getting hardware info:', error);
      setErrorMessage('Failed to get hardware information. Please try again.');
      setIsForgotPasswordActive(false);
    } finally {
      setIsLoadingHardwareInfo(false);
    }
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
    // Clear error message when user starts typing
    if (errorMessage) {
      setErrorMessage('');
    }
    if (successMessage) {
      setSuccessMessage('');
    }
  };

  const handleCopy = async (text, type) => {
    try {
      if (navigator.clipboard && window.isSecureContext) {
        await navigator.clipboard.writeText(text);
        setCopyFeedback(`${type} copied!`);
        setTimeout(() => setCopyFeedback(''), 2000);
      } else {
        // Fallback for older browsers or non-secure contexts
        const textArea = document.createElement('textarea');
        textArea.value = text;
        textArea.style.position = 'fixed';
        textArea.style.left = '-999999px';
        textArea.style.top = '-999999px';
        document.body.appendChild(textArea);
        textArea.focus();
        textArea.select();
        document.execCommand('copy');
        textArea.remove();
        setCopyFeedback(`${type} copied!`);
        setTimeout(() => setCopyFeedback(''), 2000);
      }
    } catch (error) {
      console.error('Copy failed:', error);
      setCopyFeedback(`Failed to copy ${type.toLowerCase()}`);
      setTimeout(() => setCopyFeedback(''), 3000);
    }
  };

  if (!isOpen) return null;

  return createPortal(
    <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-[999999] animate-fade-in" style={{ zIndex: 999999 }}>
      {/* Modal Container */}
      <div className="bg-white dark:bg-gray-900/70 rounded-3xl shadow-2xl w-full max-w-md mx-4 overflow-hidden transform animate-scale-in relative" style={{ zIndex: 1000000 }}>
        {/* Header with gradient */}
        <div className="bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] p-8 text-white relative">
          {/* Close button */}
          <button
            onClick={handleClose}
            className="absolute top-4 right-4 text-white/80 hover:text-white transition-colors duration-200 p-2 rounded-full hover:bg-white dark:bg-gray-900/70"
          >
            <X className="w-5 h-5" />
          </button>
          
          {/* Header content */}
          <div className="text-center">
            <div className="w-16 h-16 bg-white dark:bg-gray-900/70 rounded-full flex items-center justify-center mx-auto mb-4 backdrop-blur-sm">
              <Shield className="w-8 h-8 text-white" />
            </div>
            <h2 className="text-2xl font-bold mb-2">Secure Access</h2>
            <p className="text-white/90 text-sm">
              Enter your password to access the system
            </p>
          </div>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="p-8">
          <div className="space-y-6">
            {/* Password field */}
            <div>
              <label className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-3">
                Password
              </label>
              <div className="relative">
                <input
                  type={showPassword ? 'text' : 'password'}
                  value={password}
                  onChange={handlePasswordChange}
                  onKeyDown={handleKeyPress}
                  placeholder="Enter your password"
                  className={`w-full px-4 py-4 border-2 rounded-lg focus:ring-2 focus:ring-[#8c7ae6] focus:border-[#8c7ae6] transition-all duration-200 pr-12 text-lg ${
                    errorMessage ? 'border-red-300 focus:border-red-500 focus:ring-red-500' : 
                    successMessage ? 'border-green-300 focus:border-green-500 focus:ring-green-500' : 
                    'border-gray-200 dark:border-gray-700'
                  }`}
                  autoFocus
                  disabled={isSubmitting}
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute right-4 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 dark:text-gray-300 transition-colors p-1"
                  disabled={isSubmitting}
                >
                  {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                </button>
              </div>
              
              {/* Error Message */}
              {errorMessage && (
                <div className="mt-2 flex items-center text-red-600 text-sm">
                  <span className="mr-1"></span>
                  {errorMessage}
                </div>
              )}
              
              {/* Success Message */}
              {successMessage && (
                <div className="mt-2 flex items-center text-green-600 text-sm">
                  <span className="mr-1"></span>
                  {successMessage}
                </div>
              )}

              {/* Forgot Password Button */}
              {!showForgotPassword && (
                <div className="mt-4 text-center">
                  <button
                    type="button"
                    onClick={handleForgotPassword}
                    disabled={isLoadingHardwareInfo || isSubmitting}
                    className="text-sm text-blue-600 hover:text-blue-800 underline disabled:opacity-50 disabled:cursor-not-allowed"
                    onMouseDown={(e) => e.preventDefault()}
                  >
                    {isLoadingHardwareInfo ? 'Getting Info...' : 'Forgot Password?'}
                  </button>
                </div>
              )}

              {/* Hardware Information Display */}
              {showForgotPassword && hardwareInfo && (
                <div className="mt-4 p-4 bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-lg">
                  <div className="flex items-start">
                    <HelpCircle className="w-5 h-5 text-blue-600 dark:text-blue-400 mt-0.5 mr-3 flex-shrink-0" />
                    <div className="flex-1">
                      <h4 className="text-blue-800 dark:text-blue-300 font-semibold mb-2">Contact Support</h4>
                      <p className="text-blue-700 dark:text-blue-300 text-sm mb-3">
                        Provide this information to support to get a temporary password:
                      </p>
                      
                      <div className="space-y-2">
                        <div className="flex items-center justify-between bg-white dark:bg-gray-900/70 p-2 rounded border">
                          <span className="text-sm font-medium text-gray-700 dark:text-gray-300">MAC Address:</span>
                          <div className="flex items-center space-x-2">
                            <code className="text-sm font-mono bg-gray-100 dark:bg-gray-800 text-gray-900 dark:text-gray-100 px-2 py-1 rounded">
                              {hardwareInfo.macAddress}
                            </code>
                            <button
                              type="button"
                              onClick={() => handleCopy(hardwareInfo.macAddress, 'MAC Address')}
                              className="text-blue-600 dark:text-blue-400 hover:text-blue-800 dark:hover:text-blue-300 p-1 transition-colors"
                              title="Copy MAC address"
                            >
                              <Copy className="w-4 h-4" />
                            </button>
                          </div>
                        </div>
                        
                        <div className="flex items-center justify-between bg-white dark:bg-gray-900/70 p-2 rounded border">
                          <span className="text-sm font-medium text-gray-700 dark:text-gray-300">Date:</span>
                          <div className="flex items-center space-x-2">
                            <code className="text-sm font-mono bg-gray-100 dark:bg-gray-800 text-gray-900 dark:text-gray-100 px-2 py-1 rounded">
                              {hardwareInfo.date}
                            </code>
                            <button
                              type="button"
                              onClick={() => handleCopy(hardwareInfo.date, 'Date')}
                              className="text-blue-600 dark:text-blue-400 hover:text-blue-800 dark:hover:text-blue-300 p-1 transition-colors"
                              title="Copy date"
                            >
                              <Copy className="w-4 h-4" />
                            </button>
                          </div>
                        </div>
                      </div>

                      <div className="mt-3">
                        <button
                          type="button"
                          onClick={() => {
                            setShowForgotPassword(false);
                            setIsForgotPasswordActive(false);
                          }}
                          className="text-xs bg-blue-600 text-white px-3 py-1 rounded hover:bg-blue-700 transition-colors"
                        >
                          Close
                        </button>
                        <p className="text-xs text-gray-600 dark:text-gray-300 mt-2">
                          Send this information to support to receive your temporary password.
                        </p>
                        {copyFeedback && (
                          <div className="mt-2 text-xs text-green-600 font-medium">
                            {copyFeedback}
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>

            {/* Action buttons */}
            <div className="flex space-x-4 pt-4">
              <button
                type="button"
                onClick={handleClose}
                className="flex-1 px-6 py-4 border-2 border-gray-200 dark:border-gray-700 text-gray-700 rounded-lg hover:bg-gray-50 transition-all duration-200 font-semibold"
                disabled={isSubmitting}
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={!password.trim() || isSubmitting || isForgotPasswordActive || isLoadingHardwareInfo}
                className="flex-1 px-6 py-4 bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] text-white rounded-lg hover:from-[#7c3aed] hover:to-[#6d28d9] transition-all duration-200 font-semibold disabled:opacity-50 disabled:cursor-not-allowed shadow-lg"
                onClick={(e) => {
                  // Ensure form submission is handled properly
                  if (isForgotPasswordActive || isLoadingHardwareInfo) {
                    e.preventDefault();
                    e.stopPropagation();
                  }
                }}
              >
                {isSubmitting ? (
                  <div className="flex items-center justify-center">
                    <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin mr-2"></div>
                    Logging in...
                  </div>
                ) : (
                  <div className="flex items-center justify-center">
                    <Lock className="w-5 h-5 mr-2" />
                    Login
                  </div>
                )}
              </button>
            </div>
          </div>
        </form>
      </div>

      {/* Custom animations */}
      <style jsx>{`
        @keyframes fade-in {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
        
        @keyframes scale-in {
          from {
            opacity: 0;
            transform: scale(0.9) translateY(-20px);
          }
          to {
            opacity: 1;
            transform: scale(1) translateY(0);
          }
        }
        
        .animate-fade-in {
          animation: fade-in 0.3s ease-out;
        }
        
        .animate-scale-in {
          animation: scale-in 0.3s ease-out;
        }
      `}</style>
    </div>,
    document.body
  );
};

export default LoginModal;
