import React, { createContext, useContext, useState, useEffect } from 'react';

const ControlContext = createContext();

export const useControlModule = () => {
  const context = useContext(ControlContext);
  if (!context) {
    throw new Error('useControlModule must be used within ControlProvider');
  }
  return context;
};

export const ControlProvider = ({ children }) => {
  const [isControlEnabled, setIsControlEnabled] = useState(() => {
    const saved = localStorage.getItem('controlModuleEnabled');
    return saved ? JSON.parse(saved) : false;
  });

  useEffect(() => {
    localStorage.setItem('controlModuleEnabled', JSON.stringify(isControlEnabled));
  }, [isControlEnabled]);

  const toggleControlModule = () => {
    setIsControlEnabled(prev => !prev);
  };

  const value = {
    isControlEnabled,
    setIsControlEnabled,
    toggleControlModule
  };

  return (
    <ControlContext.Provider value={value}>
      {children}
    </ControlContext.Provider>
  );
};

export default ControlContext;
