import { useState, useCallback, useMemo, useEffect } from 'react';
import ApiService from '../services/apiService';
import { defaultConfig } from '../data/controlData';
export const useControlData = () => {
  const [config, setConfig] = useState(null);
  const [initialConfig, setInitialConfig] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isDefault, setIsDefault] = useState(false);

  useEffect(() => {
    const loadConfig = async () => {
      try {
        setIsLoading(true);
        setError(null);
        const response = await ApiService.getControlConfig();
        
        if (response.success && response.data) {
          setConfig(response.data);
          setInitialConfig(response.data);
          setIsDefault(response.is_default || false);
        } else {
          setConfig(defaultConfig);
          setInitialConfig(defaultConfig);
          setIsDefault(true);
        }
      } catch (err) {
        setError(err.message);
        setConfig(defaultConfig);
        setInitialConfig(defaultConfig);
        setIsDefault(true);
      } finally {
        setIsLoading(false);
      }
    };

    loadConfig();
  }, []);

  const updateConfigSection = useCallback((section, sectionData) => {
    setConfig(prevConfig => {
      const currentConfig = prevConfig || defaultConfig;
      
      // Handle nested object structures (inverter_control, grid_control, genset_control)
      if (section === 'inverter_control' || section === 'grid_control' || section === 'genset_control') {
        const currentSection = currentConfig[section] || {};
        // Preserve devices object if it exists and is not being updated
        const mergedSection = {
          ...currentSection,
          ...sectionData
        };
        
        // If devices exist in current section but not in sectionData, preserve them
        if (currentSection.devices && !sectionData.devices) {
          mergedSection.devices = currentSection.devices;
        }
        
        return {
          ...currentConfig,
          [section]: mergedSection
        };
      }
      
      // Handle other sections normally
      return {
        ...currentConfig,
        [section]: {
          ...(currentConfig[section] || {}),
          ...sectionData
        }
      };
    });
  }, []);

  const addInverter = useCallback((inverterData) => {
    setConfig(prevConfig => {
      const currentConfig = prevConfig || defaultConfig;
      const inverterDevices = currentConfig.inverter_control?.devices || {};
      const deviceName = inverterData.device_name || `inverter_${Object.keys(inverterDevices).length + 1}`;
      
      return {
        ...currentConfig,
        inverter_control: {
          ...(currentConfig.inverter_control || {}),
          devices: {
            ...inverterDevices,
            [deviceName]: {
              ...inverterData,
              device_name: deviceName
            }
          }
        }
      };
    });
  }, []);

  const updateInverter = useCallback((deviceName, updates) => {
    setConfig(prevConfig => {
      const currentConfig = prevConfig || defaultConfig;
      const inverterDevices = currentConfig.inverter_control?.devices || {};
      
      // Find the key for this device (could be device_name or a generated key)
      const deviceKey = Object.keys(inverterDevices).find(
        key => inverterDevices[key].device_name === deviceName || key === deviceName
      ) || deviceName;
      
      return {
        ...currentConfig,
        inverter_control: {
          ...(currentConfig.inverter_control || {}),
          devices: {
            ...inverterDevices,
            [deviceKey]: {
              ...(inverterDevices[deviceKey] || { device_name: deviceName }),
              ...updates
            }
          }
        }
      };
    });
  }, []);

  const deleteInverter = useCallback((deviceName) => {
    setConfig(prevConfig => {
      const currentConfig = prevConfig || defaultConfig;
      const inverterDevices = currentConfig.inverter_control?.devices || {};
      
      // Find and remove the device
      const updatedDevices = { ...inverterDevices };
      const deviceKey = Object.keys(updatedDevices).find(
        key => updatedDevices[key].device_name === deviceName || key === deviceName
      );
      
      if (deviceKey) {
        delete updatedDevices[deviceKey];
      }
      
      return {
        ...currentConfig,
        inverter_control: {
          ...(currentConfig.inverter_control || {}),
          devices: updatedDevices
        }
      };
    });
  }, []);

  const saveConfig = useCallback(async () => {
    if (!config) {
      throw new Error('No configuration to save');
    }

    const response = await ApiService.saveControlConfig(config);
    
    if (response.success) {
      setInitialConfig(config);
      setIsDefault(false);
      return response;
    } else {
      throw new Error(response.error || 'Failed to save configuration');
    }
  }, [config]);

  const isDirty = useMemo(() => {
    if (!config || !initialConfig) return false;
    try {
      return JSON.stringify(config) !== JSON.stringify(initialConfig);
    } catch (e) {
      return true;
    }
  }, [config, initialConfig]);

  return {
    config: config || defaultConfig,
    updateConfigSection,
    addInverter,
    updateInverter,
    deleteInverter,
    saveConfig,
    isDirty,
    isLoading,
    error,
    isDefault
  };
};

export default useControlData;
