import React from 'react';

/**
 * Reusable sortable table header component - Matching gfe-sphere exactly
 * @param {Object} props - Component props
 * @param {string} props.label - Display label for the column
 * @param {string} props.sortKey - Unique key for the column
 * @param {string} props.currentSortBy - Current sort key
 * @param {string} props.currentSortOrder - Current sort order ('ASC' | 'DESC')
 * @param {Function} props.onSort - Sort handler function
 * @param {string} props.className - Additional CSS classes
 * @returns {JSX.Element} Sortable table header
 */
const SortableTableHeader = ({
  label,
  sortKey,
  currentSortBy,
  currentSortOrder,
  onSort,
  className = '',
}) => {
  const isActive = currentSortBy === sortKey;
  const isAscending = isActive && currentSortOrder === 'ASC';
  const isDescending = isActive && currentSortOrder === 'DESC';

  return (
    <th
      className={`px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider ${className}`}
    >
      <button
        onClick={() => onSort(sortKey)}
        className="flex items-center gap-1 hover:text-gray-700 dark:hover:text-gray-200 transition-colors focus:outline-none group"
      >
        <span>{label}</span>
        <span className="flex flex-col">
          {/* Up Arrow */}
          <svg
            className={`w-3 h-3 -mb-1 transition-colors ${
              isAscending
                ? 'text-primary-600 dark:text-primary-400'
                : 'text-gray-300 dark:text-gray-600 group-hover:text-gray-400 dark:group-hover:text-gray-500'
            }`}
            fill="currentColor"
            viewBox="0 0 20 20"
          >
            <path d="M5.293 9.707a1 1 0 010-1.414l4-4a1 1 0 011.414 0l4 4a1 1 0 01-1.414 1.414L11 7.414V15a1 1 0 11-2 0V7.414L6.707 9.707a1 1 0 01-1.414 0z" />
          </svg>
          {/* Down Arrow */}
          <svg
            className={`w-3 h-3 -mt-1 transition-colors ${
              isDescending
                ? 'text-primary-600 dark:text-primary-400'
                : 'text-gray-300 dark:text-gray-600 group-hover:text-gray-400 dark:group-hover:text-gray-500'
            }`}
            fill="currentColor"
            viewBox="0 0 20 20"
          >
            <path d="M14.707 10.293a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 111.414-1.414L9 12.586V5a1 1 0 012 0v7.586l2.293-2.293a1 1 0 011.414 0z" />
          </svg>
        </span>
      </button>
    </th>
  );
};

export default SortableTableHeader;
