import React, { useState, useRef, useEffect } from 'react';
import { createPortal } from 'react-dom';
import { Link, useLocation } from 'react-router-dom';
import { useAppSelector } from '../../hooks/redux';
import { useAuth } from '../../contexts/AuthContext';
import { getBasePath } from '../../utils/helpers';
import {
  Home,
  Network,
  Server,
  FileText,
  Bell,
  Settings,
  Sliders,
  Sun,
  Moon,
  ChevronLeft,
  ChevronRight,
} from 'lucide-react';

// Tooltip component for collapsed sidebar
function NavItemTooltip({ children, label, isSidebarCollapsed }) {
  const buttonRef = useRef(null);
  const [isHovered, setIsHovered] = useState(false);
  const [position, setPosition] = useState({ left: 0, top: 0 });

  useEffect(() => {
    if (isSidebarCollapsed && isHovered && buttonRef.current) {
      const updatePosition = () => {
        const rect = buttonRef.current?.getBoundingClientRect();
        if (rect) {
          setPosition({
            left: rect.right + 8,
            top: rect.top + rect.height / 2,
          });
        }
      };
      updatePosition();
      window.addEventListener('scroll', updatePosition);
      window.addEventListener('resize', updatePosition);
      return () => {
        window.removeEventListener('scroll', updatePosition);
        window.removeEventListener('resize', updatePosition);
      };
    }
  }, [isSidebarCollapsed, isHovered]);

  return (
    <div
      className="relative group"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {React.cloneElement(children, { ref: buttonRef })}
      {isSidebarCollapsed && isHovered && createPortal(
        <div
          className="fixed px-3 py-2 bg-gray-900 text-white text-sm font-medium rounded-lg pointer-events-none transition-all duration-300 whitespace-nowrap shadow-xl"
          style={{
            left: `${position.left}px`,
            top: `${position.top}px`,
            transform: 'translateY(-50%)',
            zIndex: 9999999,
          }}
        >
          {label}
          <div className="absolute right-full top-1/2 -translate-y-1/2 border-4 border-transparent border-r-gray-900"></div>
        </div>,
        document.body
      )}
    </div>
  );
}

const Sidebar = () => {
  const location = useLocation();
  const { unreadCount } = useAppSelector((state) => state.alerts);
  const { isAuthenticated } = useAuth();

  // Sidebar collapse state
  const [isSidebarCollapsed, setIsSidebarCollapsed] = useState(() => {
    const saved = localStorage.getItem('sidebarCollapsed');
    return saved ? JSON.parse(saved) : false;
  });

  // Dark mode state (optional - can be removed if not needed)
  const [isDarkMode, setIsDarkMode] = useState(() => {
    const saved = localStorage.getItem('darkMode');
    return saved ? JSON.parse(saved) : false;
  });

  useEffect(() => {
    localStorage.setItem('sidebarCollapsed', JSON.stringify(isSidebarCollapsed));
  }, [isSidebarCollapsed]);

  useEffect(() => {
    localStorage.setItem('darkMode', JSON.stringify(isDarkMode));
    if (isDarkMode) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  }, [isDarkMode]);

  const toggleSidebar = () => setIsSidebarCollapsed(!isSidebarCollapsed);
  const toggleDarkMode = () => setIsDarkMode(!isDarkMode);

  // Define navigation items with their routes (default page is "/" so proxy base path works)
  const tabs = [
    { id: 'overview', label: 'Overview', icon: Home, path: '/', protected: false },
    { id: 'network', label: 'Network', icon: Network, path: '/network', protected: true },
    { id: 'devices', label: 'Devices', icon: Server, path: '/devices', protected: false },
    { id: 'logs', label: 'Logs', icon: FileText, path: '/logs', protected: false },
    { id: 'alerts', label: 'Alerts', icon: Bell, path: '/alerts', protected: false },
    { id: 'settings', label: 'Settings', icon: Settings, path: '/settings', protected: true },
    { id: 'control', label: 'Control', icon: Sliders, path: '/control', protected: true },
  ];

  // Filter tabs based on authentication status
  const visibleTabs = tabs.filter((tab) => !tab.protected || isAuthenticated);

  // Path relative to base (for VPN proxy: /vpn/site/neocore/0  effective path is /)
  const basePath = getBasePath();
  const effectivePath = basePath
    ? (location.pathname.replace(new RegExp('^' + basePath.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')), '') || '/')
    : location.pathname;

  // Robust active check: exact or nested path match (uses effective path when under proxy)
  const isActivePath = (path) => {
    if (path === '/') return effectivePath === '' || effectivePath === '/';
    return effectivePath === path || effectivePath.startsWith(path + '/');
  };

  return (
    <div className="relative h-screen p-[10px]">
      <div className={`h-full flex flex-col relative bg-gradient-to-b from-[#f1f3f9] to-[#8c7ae663] dark:bg-gradient-to-b dark:from-[#060a1663] dark:to-[#8c7ae663] rounded-[15px] shadow-lg transition-all duration-300 ${isSidebarCollapsed ? 'w-[80px]' : 'w-[256px]'}`}>
        
        {/* Collapse/Expand Toggle Button - External */}
        <button
          onClick={toggleSidebar}
          className={`hidden lg:flex absolute top-[80px] rounded-full bg-white dark:bg-gray-700 hover:bg-primary-500 dark:hover:bg-primary-500 hover:text-white border border-primary-500 text-primary-500 z-40 transition-all duration-300 items-center justify-center shadow-md ${
            isSidebarCollapsed ? 'right-[-12px] p-1.5' : 'right-[-6px] p-1'
          }`}
          aria-label={isSidebarCollapsed ? 'Expand sidebar' : 'Collapse sidebar'}
        >
          {isSidebarCollapsed ? (
            <ChevronRight className="w-3.5 h-3.5" />
          ) : (
            <ChevronLeft className="w-3.5 h-3.5" />
          )}
        </button>

        {/* Content */}
        <div className="relative z-10 h-full flex flex-col">
          {/* Logo at top */}
          <div className={`flex-shrink-0 px-4 py-4 border-b border-[#24292e1f] dark:border-gray-700/60 flex items-center justify-center transition-all duration-300 ${isSidebarCollapsed ? 'px-2' : ''}`}>
            {isSidebarCollapsed ? (
              <div className="w-full flex items-center justify-center">
                <img src="/nslogo.png" alt="NeoView" className="h-10 w-10 object-contain" />
              </div>
            ) : (
              <div className="flex w-full justify-center flex-col items-center gap-3">
                <img
                  src="/nslogo.png"
                  alt="NeoView"
                  className="h-10 object-contain w-[135px]"
                />
                <span className="text-lg dark:text-white -mt-2">NeoView</span>
              </div>
            )}
          </div>

          {/* Navigation Items */}
          <div className="flex-shrink-0 border-b border-gray-200/60 dark:border-gray-700/60">
            {visibleTabs.map((tab) => {
              const isActive = isActivePath(tab.path);
              const Icon = tab.icon;

              return (
                <NavItemTooltip
                  key={tab.id}
                  label={tab.label}
                  isSidebarCollapsed={isSidebarCollapsed}
                >
                  <Link
                    to={tab.path}
                    className={`relative flex items-center my-2 rounded-lg transition-all duration-300 touch-manipulation ${
                      isSidebarCollapsed
                        ? 'w-12 h-12 mx-auto justify-center items-center'
                        : 'w-[95%] mx-auto px-4 py-3 justify-between text-left'
                    } ${
                      isActive
                        ? 'bg-primary-500 text-white'
                        : 'hover:bg-gray-50 dark:hover:bg-gray-800/50'
                    }`}
                  >
                    {/* Active indicator line */}
                    {isActive && !isSidebarCollapsed && (
                      <div className="absolute left-0 top-0 bottom-0 w-1 bg-gradient-to-b from-[#8c7ae6] to-[#a78bfa] shadow-lg shadow-[#8c7ae6]/50 rounded-r-full"></div>
                    )}

                    {isSidebarCollapsed ? (
                      <div
                        className={`flex items-center justify-center transition-colors ${
                          isActive
                            ? 'text-white'
                            : 'text-gray-600 dark:text-gray-300 group-hover:text-[#8c7ae6] dark:group-hover:text-[#8c7ae6]'
                        }`}
                      >
                        <Icon size={20} />
                      </div>
                    ) : (
                      <>
                        <div className="flex items-center gap-3 flex-1 min-w-0">
                          {/* Icon */}
                          <div
                            className={`flex-shrink-0 transition-colors ${
                              isActive
                                ? 'text-white'
                                : 'text-gray-600 dark:text-gray-300 group-hover:text-[#8c7ae6]'
                            }`}
                          >
                            <Icon size={20} />
                          </div>

                          {/* Label */}
                          <span
                            className={`font-semibold text-sm sm:text-base tracking-wide transition-all duration-300 ${
                              isActive
                                ? 'text-white'
                                : 'text-gray-700 dark:text-gray-300 group-hover:text-gray-900 dark:group-hover:text-white'
                            }`}
                          >
                            {tab.label}
                          </span>

                          {/* Active badge */}
                          {isActive && (
                            <div className="ml-auto mr-2">
                              <div className="w-1.5 h-1.5 rounded-full bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] animate-pulse shadow-lg shadow-[#8c7ae6]/50"></div>
                            </div>
                          )}
                        </div>

                        {/* Alerts badge */}
                        {tab.id === 'alerts' && unreadCount > 0 && (
                          <span className="ml-auto bg-red-600 text-white text-xs px-2 py-1 rounded-full shadow">
                            {unreadCount}
                          </span>
                        )}
                      </>
                    )}
                  </Link>
                </NavItemTooltip>
              );
            })}
          </div>

          {/* Bottom Section: Theme Toggle */}
          <div className="mt-auto border-t border-gray-200/60 dark:border-gray-700/60">
            {/* Theme Toggle */}
            <div className={`p-3 sm:p-4 ${isSidebarCollapsed ? 'px-2' : ''}`}>
              <NavItemTooltip
                label={isDarkMode ? 'Light Mode' : 'Dark Mode'}
                isSidebarCollapsed={isSidebarCollapsed}
              >
                <button
                  onClick={toggleDarkMode}
                  className={`w-full flex items-center rounded-lg text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-800 transition-all duration-200 touch-manipulation group ${
                    isSidebarCollapsed ? 'justify-center px-2 py-2.5' : 'gap-3 px-4 py-3'
                  }`}
                  aria-label="Toggle dark mode"
                >
                  <div className="flex items-center justify-center w-5 h-5 text-gray-600 dark:text-gray-300 group-hover:text-[#8c7ae6] dark:group-hover:text-[#8c7ae6] transition-colors">
                    {isDarkMode ? <Sun size={20} /> : <Moon size={20} />}
                  </div>
                  <span
                    className={`text-base font-medium transition-all duration-300 ${
                      isSidebarCollapsed ? 'w-0 opacity-0 overflow-hidden' : 'opacity-100'
                    }`}
                  >
                    {isDarkMode ? 'Light Mode' : 'Dark Mode'}
                  </span>
                </button>
              </NavItemTooltip>
            </div>

            {/* Version Footer */}
            <div className={`px-3 sm:px-4 pb-3 sm:pb-4 ${isSidebarCollapsed ? 'px-2' : ''}`}>
              <div
                className={`text-xs text-gray-500 dark:text-gray-300 text-center transition-all duration-300 ${
                  isSidebarCollapsed ? 'opacity-0' : 'opacity-100'
                }`}
              >
                <div className="font-medium">v1.0.0</div>
                <div className="text-[10px] mt-1"> 2025 NeoView</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;
