import React, { useState, useEffect } from "react";
import {
  ResponsiveContainer,
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  ReferenceLine,
} from "recharts";
import { useAppSelector } from "../hooks/redux";
import Spinner from "./Spinner";

/* -------------------------- API Data Fetching -------------------------- */
const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5001';


// put near the top
const toKW = (v) => {
  if (v === null || v === undefined || Number.isNaN(+v)) return 0;
  const n = Number(v);
  // Heuristic: if magnitude >= 1000, it's probably Watts, convert to kW
  return Math.abs(n) >= 1000 ? n / 1000 : n;
};


const fetchPowerFlowData = async (hours = 1) => {
  try {
    const response = await fetch(`${API_BASE_URL}/power-flow/history?hours=${hours}`);
    const result = await response.json();
    
   if (result.success && result.data) {
    return result.data.map(item => {
      const ts = new Date(item.time);
      return {
        ts,                         // keep Date for tooltip formatting
        tsNum: ts.getTime(),        // use this for XAxis (number)
        pv: toKW(item.solar),
        genset: toKW(item.genset),
        load: toKW(item.load),
        grid: toKW(item.grid),
        batchId: item.batchId
      };
    });
  }

    return [];
  } catch (error) {
    console.error('Error fetching power flow data:', error);
    return [];
  }
};

/* ----------------------------- Professional Tooltip ----------------------------- */
const CustomTooltip = ({ active, payload }) => {
  if (!active || !payload || !payload.length) return null;
  const d = payload[0].payload;
  
  // Format time with seconds for 5-second precision
  const dt = d.ts.toLocaleString("en-GB", {
    weekday: "short",
    hour: "2-digit",
    minute: "2-digit",
    second: "2-digit",
    day: "2-digit",
    month: "short",
  });

  const rows = [
    { key: "pv", label: "Solar Generation", color: "#f59e0b" },
    { key: "grid", label: "Grid", color: "#3b82f6" },
    { key: "genset", label: "Generator Output", color: "#10b981" },
    { key: "load", label: "Load Demand", color: "#ef4444" },
  ];

  // Check if dark mode is active
  const isDarkMode = document.documentElement.classList.contains('dark');
  
  return (
    <div
      className="rounded-lg border-0 shadow-2xl"
      style={{
        background: isDarkMode 
          ? "linear-gradient(135deg, rgba(17, 24, 39, 0.95) 0%, rgba(31, 41, 55, 0.95) 100%)"
          : "linear-gradient(135deg, rgba(255, 255, 255, 0.95) 0%, rgba(248, 250, 252, 0.95) 100%)",
        backdropFilter: "blur(20px)",
        minWidth: 300,
        maxWidth: 350,
        padding: "16px 20px",
        boxShadow: "0 25px 50px rgba(0,0,0,0.25), 0 0 0 1px rgba(255,255,255,0.1)",
        border: isDarkMode ? "1px solid rgba(75, 85, 99, 0.3)" : "1px solid rgba(255,255,255,0.2)",
      }}
    >
            {/* Header */}
      <div
        style={{
          marginBottom: 12,
          paddingBottom: 12,
          borderBottom: "2px solid rgba(0, 151, 178, 0.2)",
          position: "relative"
        }}
      >
        <div style={{ fontSize: 14, fontWeight: 800, color: isDarkMode ? "#ffffff" : "#010101", marginBottom: 4, textTransform: "uppercase", letterSpacing: "0.5px" }}>
          Power Flow Analysis
        </div>
        <div style={{ fontSize: 12, color: isDarkMode ? "#d1d5db" : "#374151", fontWeight: 600 }}>{dt}</div>
        <div style={{ position: "absolute", top: 0, right: 0, width: "40px", height: "2px", background: "linear-gradient(90deg, #8c7ae6, #a78bfa)", borderRadius: "1px" }} />
      </div>

      {/* Series values - super clean */}
      <div style={{ display: "grid", gap: 8 }}>
        {rows.map((r) => (
          <div
            key={r.key}
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              padding: "12px 16px",
              borderRadius: 12,
              background: isDarkMode 
                ? "linear-gradient(135deg, rgba(31,41,55,0.8) 0%, rgba(17,24,39,0.8) 100%)"
                : "linear-gradient(135deg, rgba(255,255,255,0.8) 0%, rgba(248,250,252,0.8) 100%)",
              border: isDarkMode ? "1px solid rgba(75, 85, 99, 0.6)" : "1px solid rgba(226, 232, 240, 0.6)",
              transition: "all 0.3s ease",
              position: "relative",
              overflow: "hidden",
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.transform = "translateY(-2px)";
              e.currentTarget.style.boxShadow = "0 8px 25px rgba(0,0,0,0.15)";
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.transform = "translateY(0)";
              e.currentTarget.style.boxShadow = "none";
            }}
          >
            <div style={{ position: "absolute", left: 0, top: 0, bottom: 0, width: "4px", background: r.color, borderRadius: "0 2px 2px 0" }} />
            <span style={{ fontSize: 13, fontWeight: 600, color: isDarkMode ? "#d1d5db" : "#374151", marginLeft: "8px" }}>{r.label}</span>
            <span style={{ fontSize: 13, fontWeight: 800, color: r.color, textShadow: "0 1px 2px rgba(0,0,0,0.1)" }}>
              {Math.abs(d[r.key]).toFixed(1)} kW
            </span>
          </div>
        ))}
      </div>
    </div>
  );
};

/* ------------------------------- Helpers ------------------------------- */
const formatXAxisTick = (tickValue) =>
  new Date(tickValue).toLocaleString("en-GB", { hour: "2-digit", minute: "2-digit" });

/* ------------------------------ Main Chart ------------------------------ */
const PowerFlowLast24h = () => {
  // Track dark mode for dynamic axis colors
  const [isDarkMode, setIsDarkMode] = React.useState(() => 
    document.documentElement.classList.contains('dark')
  );

  // Listen for theme changes
  React.useEffect(() => {
    const observer = new MutationObserver(() => {
      setIsDarkMode(document.documentElement.classList.contains('dark'));
    });
    
    observer.observe(document.documentElement, {
      attributes: true,
      attributeFilter: ['class']
    });
    
    return () => observer.disconnect();
  }, []);
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  // Time range options (10 minutes to 2 hours)
  const timeRangeOptions = [
    { value: 1/6, label: '10 min' },    // 10 minutes
    { value: 1/3, label: '20 min' },    // 20 minutes
    { value: 1/2, label: '30 min' },    // 30 minutes
    { value: 1, label: '1 hour' },      // 1 hour
    { value: 1.5, label: '1.5 hours' }, // 1.5 hours
    { value: 2, label: '2 hours' }      // 2 hours
  ];

  const [selectedHours, setSelectedHours] = useState(1/6); // Default: 10 minutes
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const [lastProcessedBatchId, setLastProcessedBatchId] = useState(null);
  
  // Get real-time data from Redux state (same as KPI component)
  const { powerFlowData } = useAppSelector((state) => state.sensor);

  // Process real-time data and append to chart
  useEffect(() => {
    if (powerFlowData && powerFlowData.timestamp) {
      console.log(' Frontend Power Flow Debug - Received data:', powerFlowData);
      
      // Use timestamp as unique identifier since batchId is undefined
      const uniqueId = powerFlowData.timestamp.toString();
      
      // Check if we've already processed this timestamp
      if (lastProcessedBatchId === uniqueId) {
        console.log(' Frontend Power Flow Debug - Already processed this timestamp:', uniqueId);
        return;
      }
      
      setData(prevData => {
        // Check if this is a new data point using timestamp
        const existingTimestamps = new Set(prevData.map(d => d.timestamp?.toString()));
        if (existingTimestamps.has(uniqueId)) {
          return prevData;
        }
        
        // Create new data point with proper formatting
        const ts = new Date(powerFlowData.timestamp || powerFlowData.time || Date.now());
        const newDataPoint = {
          ts,
          tsNum: ts.getTime(),
          pv: toKW(powerFlowData.solar),
          genset: toKW(powerFlowData.genset),
          load: toKW(powerFlowData.load),
          grid: toKW(powerFlowData.grid),
          batchId: uniqueId,
          timestamp: powerFlowData.timestamp
        };

        
        console.log(' Frontend Power Flow Debug - New data point:', {
          solar: powerFlowData.solar,
          parsedSolar: parseFloat(powerFlowData.solar),
          pv: newDataPoint.pv,
          grid: powerFlowData.grid,
          parsedGrid: parseFloat(powerFlowData.grid),
          gridValue: newDataPoint.grid
        });
        
        console.log(' Frontend Power Flow Debug - Complete newDataPoint:', newDataPoint);
        
        if (prevData.length === 0) {
          setLastProcessedBatchId(uniqueId);
          return [newDataPoint];
        }
        
        // Smoothly append new data point to the end without re-rendering entire chart
        // Use functional update to prevent unnecessary re-renders
        const updatedData = [...prevData, newDataPoint];
        
        // Maintain optimal data size for smooth performance
        // For 10 minutes: show up to 120 points (every 5 seconds)
        // For 2 hours: show up to 1440 points (every 5 seconds)
        const maxPoints = Math.min(selectedHours * 720, updatedData.length);
        const finalData = updatedData.slice(-maxPoints);
        
        // Update the last processed timestamp
        setLastProcessedBatchId(uniqueId);
        
        // Return the same array reference if no change needed (prevents re-render)
        if (finalData.length === prevData.length && 
            finalData[finalData.length - 1]?.timestamp === prevData[prevData.length - 1]?.timestamp) {
          return prevData;
        }
        
        return finalData;
      });
    }
  }, [powerFlowData, selectedHours, lastProcessedBatchId]);

  useEffect(() => {
    // Fetch initial data ONLY ONCE when component mounts
    const loadInitialData = async () => {
      setIsLoading(true);
      try {
        const initialData = await fetchPowerFlowData(selectedHours);
        
        setData(initialData);
      } catch (error) {
        console.error('Error loading initial data:', error);
      } finally {
        setIsLoading(false);
      }
    };

    // Load initial data once
    loadInitialData();
  }, [selectedHours]); // Include selectedHours as dependency

  // Handle time range change with smooth transitions
  const handleTimeRangeChange = async (hours) => {
    setSelectedHours(hours);
    setIsDropdownOpen(false);
    
    // Check if we already have enough data for the new time range
    const requiredDataPoints = hours * 720; // 720 data points per hour (5-second intervals)
    const currentDataPoints = data.length;
    
    if (currentDataPoints >= requiredDataPoints) {
      return;
    }
    
    // Only fetch new data if we don't have enough
    setIsLoading(true);
    
    try {
      const newData = await fetchPowerFlowData(hours);
      
      // Smooth transition by keeping some old data initially
      setData(prevData => {
        if (prevData.length > 0) {
          // Keep last few points for smooth transition
          const transitionData = [...prevData.slice(-3), ...newData];
          return transitionData;
        }
        return newData;
      });
    } catch (error) {
      console.error('Error changing time range:', error);
    } finally {
      setIsLoading(false);
    }
  };

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (isDropdownOpen && !event.target.closest('.dropdown-container')) {
        setIsDropdownOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [isDropdownOpen]);

  // Show loading spinner only when actually loading
  if (isLoading) {
    return (
      <div className="h-full rounded-lg border border-gray-200 dark:border-gray-700 p-4 flex flex-col overflow-hidden bg-white dark:bg-gray-900/70">
        <div className="flex-shrink-0 mb-3">
          <h3 className="text-xl font-bold mb-1 text-gray-900 dark:text-white">
            Power Flow Analysis
          </h3>
          <div className="h-1 w-24 bg-gray-900 dark:bg-gray-200 rounded-full"></div>
        </div>
        <div className="flex-grow rounded-lg border border-gray-200 dark:border-gray-700 p-6 flex items-center justify-center">
          <Spinner size="lg" />
        </div>
      </div>
    );
  }

  return (
    <div
      className="h-full rounded-lg border border-gray-200 dark:border-gray-700 p-4 flex flex-col overflow-hidden bg-white dark:bg-gray-900/70"
      style={{ boxShadow: "0 10px 25px -5px rgba(0,0,0,0.1)" }}
    >
      {/* Header */}
      <div className="flex-shrink-0 mb-3 flex items-center justify-between">
        <div>
          <h3 className="text-xl font-bold mb-1 text-gray-900 dark:text-white">
            Power Flow Analysis
          </h3>
          <div className="h-1 w-24 bg-gray-900 dark:bg-gray-200 rounded-full"></div>
        </div>
        
        {/* Time Range Dropdown */}
        <div className="flex items-center gap-3">
          <div className="relative dropdown-container">
            <button
              onClick={() => setIsDropdownOpen(!isDropdownOpen)}
              className="flex items-center gap-2 px-4 py-2.5 bg-white backdrop-blur-sm border border-gray-200 dark:border-gray-700 rounded-lg shadow-sm hover:bg-white dark:bg-gray-900/70 transition-all duration-200"
            >
              <svg className="w-4 h-4 text-gray-600 dark:text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
                {timeRangeOptions.find(opt => opt.value === selectedHours)?.label || '10 min'}
              </span>
              <svg className={`w-4 h-4 text-gray-600 dark:text-gray-300 transition-transform duration-200 ${isDropdownOpen ? 'rotate-180' : ''}`} fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </button>
            
            {/* Dropdown Menu */}
            {isDropdownOpen && (
              <div className="absolute right-0 top-full mt-1 w-48 bg-white dark:bg-gray-900/70 border border-gray-200 dark:border-gray-700 rounded-lg shadow-2xl z-50 overflow-hidden">
                <div className="py-1">
                  {timeRangeOptions.map((option) => (
                    <button
                      key={option.value}
                      onClick={() => handleTimeRangeChange(option.value)}
                      className={`w-full px-4 py-2.5 text-left text-sm transition-all duration-200 flex items-center justify-between group ${
                        selectedHours === option.value 
                          ? 'bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] text-white shadow-md' 
                          : 'text-gray-700 hover:bg-gradient-to-r hover:from-gray-50 hover:to-gray-100'
                      }`}
                    >
                      <span className="font-medium">{option.label}</span>
                      {selectedHours === option.value ? (
                        <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                          <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                        </svg>
                      ) : (
                        <div className="w-4 h-4 opacity-0 group-hover:opacity-100 transition-opacity duration-200">
                          <svg className="w-4 h-4 text-[#8c7ae6]" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                          </svg>
                        </div>
                      )}
                    </button>
                  ))}
                </div>
              </div>
            )}
          </div>
          
          {/* Socket Status Indicator */}
          <div className="flex items-center gap-2 px-3 py-2 bg-white dark:bg-gray-900/70 backdrop-blur-sm rounded-lg border border-gray-200 dark:border-gray-700 shadow-sm">
            <div className={`w-2 h-2 rounded-full ${powerFlowData ? 'bg-green-500 animate-pulse' : 'bg-gray-400'}`}></div>
            <span className="text-xs text-gray-600 dark:text-gray-300">
              {powerFlowData ? 'Live' : 'Waiting for Data'}
            </span>
          </div>
        </div>
      </div>

      {/* Chart */}
      <div className="flex-grow rounded-lg border border-gray-200 dark:border-gray-700 p-2 min-h-0 bg-white dark:bg-gray-900/70 transition-all duration-300 ease-in-out">
        {console.log(' Chart Debug - Data for chart:', data.slice(-3))}
        {data.length === 0 ? (
          <div className="h-full flex items-center justify-center">
            <div className="text-center">
              <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] flex items-center justify-center">
                <svg className="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                </svg>
              </div>
              <h3 className="text-lg font-semibold text-gray-700 dark:text-gray-300 mb-2">No Data Available</h3>
              <p className="text-sm text-gray-500 dark:text-gray-300 mb-1">No power flow data found for the selected time range.</p>
              <p className="text-xs text-gray-400 dark:text-gray-500">Data will appear here once devices start sending information.</p>
            </div>
          </div>
        ) : (
          <ResponsiveContainer width="100%" height="100%">
            <LineChart 
              key={data.length} // Force re-render when data changes
              data={data}
              margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
              animationDuration={150}
              animationEasing="ease-out"
            >
            {/* Zero line */}
            <ReferenceLine y={0} stroke="#94a3b8" strokeDasharray="3 3" strokeWidth={1.2} />


            {/* Axes */}
            <XAxis
              dataKey="tsNum"    
              type="number"
              scale="time"
              domain={["dataMin", "dataMax"]}
              tickFormatter={formatXAxisTick}
              tick={{ fontSize: 11, fill: isDarkMode ? "#d1d5db" : "#374151", fontWeight: 600 }}
              axisLine={{ stroke: isDarkMode ? "#4b5563" : "#6b7280", strokeWidth: 1 }}
              tickLine={{ stroke: isDarkMode ? "#4b5563" : "#6b7280", strokeWidth: 1 }}
              interval="preserveStartEnd"
              minTickGap={40}
            />
            <YAxis
              label={{
                value: "Power (kW)",
                angle: -90,
                position: "insideLeft",
                style: { textAnchor: "middle", fill: isDarkMode ? "#d1d5db" : "#374151", fontSize: 12, fontWeight: 700 },
              }}
              tick={{ fontSize: 11, fill: isDarkMode ? "#d1d5db" : "#374151", fontWeight: 600 }}
              axisLine={{ stroke: isDarkMode ? "#4b5563" : "#6b7280", strokeWidth: 1 }}
              tickLine={{ stroke: isDarkMode ? "#4b5563" : "#6b7280", strokeWidth: 1 }}
              domain={["dataMin - 50", "dataMax + 50"]}
            />

            {/* Lines */}
            {/* Power Flow Lines */}
            <Line 
              type="monotone" 
              dataKey="pv" 
              name="Solar Generation"
              stroke="#f59e0b" 
              strokeWidth={1.5}
              dot={false}
              activeDot={{ r: 4, fill: "#f59e0b" }}
              animationDuration={150}
              animationEasing="ease-out"
              isAnimationActive={true}
            />
            <Line 
              type="monotone" 
              dataKey="grid" 
              name="Grid"
              stroke="#3b82f6" 
              strokeWidth={1.5}
              dot={false}
              activeDot={{ r: 4, fill: "#3b82f6" }}
              animationDuration={150}
              animationEasing="ease-out"
              isAnimationActive={true}
            />
            <Line 
              type="monotone" 
              dataKey="genset" 
              name="Generator Output"
              stroke="#10b981" 
              strokeWidth={1.5}
              dot={false}
              activeDot={{ r: 4, fill: "#10b981" }}
              animationDuration={150}
              animationEasing="ease-out"
              isAnimationActive={true}
            />
            <Line 
              type="monotone" 
              dataKey="load" 
              name="Load Demand"
              stroke="#ef4444" 
              strokeWidth={1.5}
              dot={false}
              activeDot={{ r: 4, fill: "#ef4444" }}
              animationDuration={150}
              animationEasing="ease-out"
              isAnimationActive={true}
            />

            <Tooltip content={<CustomTooltip />} />
            <Legend
              wrapperStyle={{ paddingTop: 6, fontSize: 11, fontWeight: 600 }}
              iconType="line"
              layout="horizontal"
              verticalAlign="bottom"
              align="center"
            />
          </LineChart>
        </ResponsiveContainer>
        )}
      </div>
    </div>
  );
};

export default PowerFlowLast24h;
