import React, { createContext, useContext, useState, useEffect } from 'react';
import { toast } from 'react-toastify';
import apiService from '../services/apiService';
import FullScreenLoader from '../components/FullScreenLoader';
import { useFullScreenLoader } from '../hooks/useFullScreenLoader';

const AuthContext = createContext();

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [token, setToken] = useState(localStorage.getItem('authToken'));
  const [isLoading, setIsLoading] = useState(true);
  
  // Full screen loader hook
  const { isVisible: loaderVisible, message: loaderMessage, type: loaderType, showLoader, hideLoader } = useFullScreenLoader();

  // Check token validity on app start only
  useEffect(() => {
    const checkAuthStatus = async () => {
      const storedToken = localStorage.getItem('authToken');
      if (storedToken && !isAuthenticated) {
        try {
          const response = await apiService.request('/auth/verify', {
            method: 'GET',
            headers: {
              'Authorization': `Bearer ${storedToken}`
            }
          });
          
          if (response.success) {
            setToken(storedToken);
            setIsAuthenticated(true);
          } else {
            // Token is invalid, remove it
            localStorage.removeItem('authToken');
            setToken(null);
            setIsAuthenticated(false);
          }
        } catch (error) {
          console.error('Token verification failed:', error);
          localStorage.removeItem('authToken');
          setToken(null);
          setIsAuthenticated(false);
        }
      }
      setIsLoading(false);
    };

    checkAuthStatus();
  }, []); // Only run on mount

  const login = async (password, showToasts = true) => {
    try {
      setIsLoading(true);
      const response = await apiService.request('/auth/login', {
        method: 'POST',
        body: JSON.stringify({ password })
      });

      if (response.success) {
        const authToken = response.token;
        localStorage.setItem('authToken', authToken);
        setToken(authToken);
        setIsAuthenticated(true);
        
        // Show beautiful loader for 3 seconds
        showLoader('Login Successful', 3000, 'login');
        
        if (showToasts) {
          // Delay toast to show after loader
          setTimeout(() => {
            toast.success('Login successful! Welcome to the system.');
          }, 3000);
        }
        return { success: true };
      } else {
        if (showToasts) {
          toast.error(response.message || 'Login failed');
        }
        return { success: false, message: response.message };
      }
    } catch (error) {
      console.error('Login error:', error);
      // Show the actual error message from the server
      const errorMessage = error.message || 'Login failed. Please try again.';
      
      if (showToasts) {
        // Enhanced error messages based on common scenarios
        if (errorMessage.includes('Invalid password') || errorMessage.includes('password')) {
          toast.error(' Invalid password. Please try again.');
        } else if (errorMessage.includes('required')) {
          toast.error(' Password is required.');
        } else if (errorMessage.includes('network') || errorMessage.includes('fetch')) {
          toast.error(' Network error. Please check your connection.');
        } else {
          toast.error(` ${errorMessage}`);
        }
      }
      
      return { success: false, message: errorMessage };
    } finally {
      setIsLoading(false);
    }
  };

  const logout = () => {
    // Show beautiful loader for 3 seconds
    showLoader('Logging Out', 3000, 'logout');
    
    // Perform logout actions after a brief delay for smooth UX
    setTimeout(() => {
      setToken(null);
      setIsAuthenticated(false);
      localStorage.removeItem('authToken');
      
      // Show toast after loader
      setTimeout(() => {
        toast.info('Logged out successfully. You are now in guest mode.');
        
        // Redirect to default page (stays under base path when behind VPN proxy)
        const base = typeof document !== 'undefined' && document.querySelector('base')?.href
          ? (() => { try { const p = new URL(document.querySelector('base').href, window.location.origin).pathname; return p.replace(/\/$/, '') || ''; } catch (e) { return ''; } })()
          : '';
        window.location.href = base + '/';
      }, 500);
    }, 2500);
  };

  const changePassword = async (newPassword) => {
    try {
      setIsLoading(true);
      const response = await apiService.request('/auth/change-password', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ newPassword })
      });

      if (response.success) {
        toast.success(' Password changed successfully! Your new password is now active.');
        return { success: true };
      } else {
        toast.error(response.message || 'Password change failed');
        return { success: false, message: response.message };
      }
    } catch (error) {
      console.error('Password change error:', error);
      
      // Enhanced error messages for password change
      const errorMessage = error.message || 'Password change failed. Please try again.';
      
      if (errorMessage.includes('New password') || errorMessage.includes('6 characters')) {
        toast.error(' New password must be at least 6 characters long.');
      } else if (errorMessage.includes('network') || errorMessage.includes('fetch')) {
        toast.error(' Network error. Please check your connection.');
      } else {
        toast.error(` ${errorMessage}`);
      }
      
      return { success: false, message: errorMessage };
    } finally {
      setIsLoading(false);
    }
  };

  const value = {
    isAuthenticated,
    isLoading,
    login,
    logout,
    changePassword,
    token
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
      {/* Full Screen Loader */}
      <FullScreenLoader 
        isVisible={loaderVisible} 
        message={loaderMessage}
        type={loaderType}
      />
    </AuthContext.Provider>
  );
};
